<template>
  <div class="Desimal container">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-8 text-center">
        <div style="margin-top: 80px">
          <span class="color-main h5">ฝากเงินทศนิยม</span>
          <div class="container mt-2">
            <div class="card card-desimal" v-if="datadesimal === null">
              <div class="card-body text-left text-dark font-weight-bold">
                <span class="font-weight-bold"> กรอกจำนวนเงินที่จะฝาก</span>
                <input
                  type="text"
                  class="form-control text-right bg-input-register"
                  id="withdraw_amount"
                  :maxlength="9"
                  v-model="withdraw_amount"
                  required
                  v-mask="{
                    alias: 'currency',
                    prefix: '',
                    groupSeparator: '',
                  }"
                  :disabled="loadersubmitdesimal"
                />
                <div class="container">
                  <div class="row justify-content-center">
                    <div
                      v-for="(btns, index) in btn.payload"
                      :key="index"
                      class="col-4 p-2 text-center"
                    >
                      <button
                        class="btn-desimal btn-sm btn btn-block font-weight-bold py-2"
                        style="    white-space: nowrap;font-size:12px"
                        @click="checkbtn(btns)"
                      >
                        {{ btns }}
                      </button>
                    </div>
                    <div class="col-12 mt-2">
                      <button
                        @click="getdatadesiaml()"
                        v-if="!loadersubmitdesimal"
                        class="btn btn-submit-desimal btn-block text-white"
                      >
                        ยืนยันการฝากเงิน
                      </button>
                      <mdb-btn
                        v-else
                        color=""
                        class="btn btn-submit-desimal btn-block text-white"
                        disabled
                      >
                        <span
                          class="spinner-grow spinner-grow-sm"
                          role="status"
                          aria-hidden="true"
                        ></span>
                        Loading...
                      </mdb-btn>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div v-if="datadesimal === null">
              <div class="card-yellow mt-3  py-3" style="">
                <div class="card-body row px-4" style="  border:20px"></div>
                <div class="row text-center">
                  <div class="col-12" style="margin-top:-10px;">
                    <p
                      class="text-dark font-weight-bold mb-0"
                      style="margin-top: -19px;"
                    >
                      กฎการฝากเงิน
                    </p>
                  </div>

                  <div class="col text-left">
                    <p class="text-dark px-2 mb-0">
                      <small> 1. ฝากเงินไม่มีขั้นต่ำ </small>
                    </p>
                    <p class="text-dark px-2 mb-0">
                      <small> 2. เช็คเบอร์ก่อนทำรายการฝากทุกครั้ง </small>
                    </p>
                    <p class="text-dark px-2 mb-0">
                      <small>
                        3.ให้ลูกค้าใช้เบอร์หรือบัญชีทรูวอลเล็ตฝากเข้ามาเท่านั้น
                      </small>
                    </p>
                    <p class="text-dark px-2 mb-0">
                      <small>
                        4.ห้ามนำบัญชีธนาคารโอนเข้าทรูวอลเล็ต
                      </small>
                    </p>
                    <p class="text-dark px-2 mb-0">
                      <small>
                        5.ในกรณีที่ลูกค้าใช้เบอร์ที่สมัครสมาชิกฝากเงินเข้ามา
                        ยอดเงินของท่านจะเข้าระบบอัติโนมัติ</small
                      >
                    </p>
                    <p class="text-dark px-2 mb-0">
                      <small>
                        6.หากท่านทำรายการฝากเข้ามาจากเบอร์อื่น
                        (ที่ไม่ได้เป็นสมาชิก) เข้ามา ต้องทำการยืนยันยอดฝาก
                        โดยการกรอกข้อมูลด้านล่างและกดยืนยันยอด
                      </small>
                    </p>
                    <p class="text-dark px-2 mb-0">
                      <small>
                        7.ลูกค้าทุกท่านต้องมียอดเงินต่ำกว่า 10 บาท มิเช่นนั้น
                        ยอดเทิร์นจะถูกคิดรวมกับของเก่า
                      </small>
                    </p>
                  </div>
                </div>
              </div>
              <div class="card-red mt-3  py-3" style="">
                <div class="card-body row px-4" style="  border:20px"></div>
                <div class="row text-center">
                  <div class="col-12" style="margin-top:-10px;">
                    <p
                      class="text-dark font-weight-bold mb-0"
                      style="margin-top: -19px;"
                    >
                      คำแนะนำการฝากเงิน
                    </p>
                  </div>

                  <div class="col text-left">
                    <p class="text-dark px-2 mb-0">
                      <small>
                        - สมาชิก จะต้องมียอดเครดิตคงเหลือในระบบต่ำกว่า 10 บาท
                        มิเช่นนั้น ระบบจะนับรวมยอดที่ต้องทำรวมกับยอดก่อนหน้านี้
                      </small>
                    </p>
                  </div>
                </div>
              </div>
            </div>

            <div v-else>
              <div
                class="card card-desimal mt-3 text-center"
                v-if="datadesimal"
              >
                <div class="card-body text-dark">
                  <p class="font-weight-bold text-blue font-weight-bold">
                    จำนวนเงินที่ต้องการโอน
                  </p>

                  <span class="h4"> {{ datadesimal.amount }}</span>
                  <br />

                  <small class="color-main">
                    <vac
                      :end-time="endtimedesimal"
                      @finish="handleCountdownEnd"
                    >
                      <template v-slot:process="{ timeObj }">
                        <span>{{
                          `กรุณาทำการโอนเงินภายใน: ${timeObj.m}:${timeObj.s}`
                        }}</span>
                      </template>
                      <template v-slot:finish>
                        <span>Done!</span>
                      </template>
                    </vac>
                    นาที</small
                  >
                </div>
              </div>
              <div
                class="card card-desimal mt-3 text-center"
                v-if="datadesimal"
              >
                <div class="card-body text-dark">
                  <span class="text-blue font-weight-bold"
                    >กรุณาฝากเข้าบัญชีนี้</span
                  >
                  <div class="container mt-3">
                    <div class="row justify-content-center text-center">
                      <div class="col-5 align-self-center">
                        <img
                          :src="
                            '/images/bank/tangle/' +
                              datadesimal.bank_code +
                              '.png'
                          "
                          width="100px"
                          alt=""
                        />
                      </div>
                      <div
                        class="col-7 align-self-center text-left font-weight-bold"
                      >
                        <small class="font-weight-bold">
                          {{ datadesimal.bank_name }} <br />
                          {{ bankNumberFormat(datadesimal.number) }}
                          <a
                            v-clipboard:copy="datadesimal.number"
                            v-clipboard:success="onCopy"
                          >
                            <mdb-badge color="warning">
                              <small>คัดลอก</small>
                            </mdb-badge>
                          </a>
                          <br />
                          {{ datadesimal.name }}</small
                        >
                      </div>
                      <div class="row">
                        <div class="col-6 text-left align-self-center">
                          <mdb-switch
                            v-model="switchValue"
                            offLabel
                            onLabel
                            classes="success pt-2"
                            @getValue="setBonus()"
                          />
                        </div>
                        <div
                          class="col-6 text-left align-self-center text-dark"
                          style="white-space: nowrap;    "
                          v-if="bonus_config"
                        >
                          <div
                            v-if="!loadertitlebonus"
                            style="    margin-left: -22px;"
                          >
                            {{ bonus_config.title }}
                          </div>
                          <div
                            class="spinner-grow text-warning"
                            role="status"
                            v-else
                          >
                            <span class="sr-only">Loading...</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="text-left py-3 ml-2">
          <router-link to="/deposit" class="btn btn-sm btn-main"
            >ย้อนกลับ</router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const jwt = require("jsonwebtoken");
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      btn: {
        payload: [50, 100, 150, 200, 300, 500, 700, 1000, 3000],
      },
      withdraw_amount: null,
      bonus_config: null,
      switchValue: true,
      loadertitlebonus: false,
      loadersubmitdesimal: false,
      datadesimal: null,
      endtimedesimal: null,
      urlapi: null,
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  methods: {
    handleCountdownEnd() {
      this.datadesimal = null;
    },
    bankNumberFormat(str) {
      return str.replace(/[0-9]/g, (x, index) => {
        if (index == 2 || index == 5) return x + "-";
        return x;
      });
    },
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
    checkbtn(value) {
      this.withdraw_amount = value;
    },
    getdatadesiaml() {
      this.loadersubmitdesimal = true;
      let payload = {
        amount: this.withdraw_amount,
      };
      let token = this.$jwt.sign(payload, this.$keypayload, {
        expiresIn: "9999s",
      });
      this.$axios
        .post(
          "https://slotxo24hr.thezeus.tech/v1/api/gen_deposit",
          { token },
          this.token
        )
        .then((result) => {
          this.loadersubmitdesimal = false;
          if (result.data.code === 0) {
            this.datadesimal = result.data.msg;
            this.endtimedesimal = this.$momentjs(result.data.msg.end_date);
            // console.log(this.endtimedesimal);
          } else {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: result.data.msg,
              type: "error",
              showConfirmButton: false,
            });
          }
        })
        .catch((err) => {
          this.loadersubmitdesimal = false;
          this.$swal({
            title: "เกิดข้อผิดพลาด",
            text: err.data.data.msg,
            type: "error",
            showConfirmButton: false,
          });
        });
    },

    onCopy: function(e) {
      this.$notify.success({
        message: "คัดลอกเลขบัญชี : " + e.text,
        position: "top right",
        timeOut: 5000,
      });
      //   this.$swal({
      //     title: "คัดลอกเลขบัญชี",
      //     text: "คัดลอกเลขบัญชี : " + e.text,
      //     icon: "success",
      //     allowEscapeKey: false,
      //     allowOutsideClick: false,
      //     showConfirmButton: true,
      //   });
    },
    getBonus() {
      this.loadertitlebonus = true;
      this.$axios
        .get("/bonus", this.token)
        .then((result) => {
          this.loadertitlebonus = false;
          this.bonus_config = result.data.payload;
        })
        .catch(function(error) {
          this.loadertitlebonus = false;
          console.log(error);
        });
    },
    loadData() {
      this.getBonus();
    },
    setBonus: function() {
      let payload = {
        is_bonus: this.switchValue === false ? 1 : 0,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "3000s",
      });

      this.$axios
        .post("/is_bonus", { token: token }, this.token)
        .then((response) => {
          if (response.data.code === "SUCCESS") {
            this.$axios
              .get("/is_login", this.token)
              .then((response) => {
                if (response.data.msg != "LOGOUT") {
                  this.$session.set("isLogin", true);
                  this.$session.set("token", response.data);
                  this.storeLogin(response.data);
                  this.loadData();
                } else {
                  this.$swal({
                    title: "เกิดข้อผิดพลาด",
                    text: "มีการเข้าสู่ระบบจากที่อื่น",
                    type: "error",
                    timer: 3000,
                    showConfirmButton: false,
                  });
                  this.$router.push("/logout");
                }
              })
              .catch(function(error) {
                console.log(error);
              });
          } else {
            console.log("false");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
  mounted() {
    this.urlapi = this.$urlapi.replace("api", "");

    if (this.token) {
      this.switchValue = this.user.is_bonus === 1 ? true : false;
      this.loadData();
    } else {
      this.$router.push("/logout");
    }
  },
};
</script>

<style></style>
