import Vue from 'vue'
import Router from 'vue-router'
import Header from './views/Header.vue'
import Login from './views/Login.vue'
import Main from './views/Main.vue'
import Register from './views/Register.vue'
import Redirect from './views/Redirect.vue'
import Logout from './views/Logout.vue'
import Deposit from './views/Deposit.vue'
import Withdraw from './views/Withdraw.vue'
import Bonus from './views/Bonus.vue'
import Affiliate from './views/Affiliate.vue'
import History from './views/History.vue'
import Profile from './views/Profile.vue'
import Footer from './views/Footer.vue'
import Otp from './views/Otp.vue'
import Dowload from './views/Dowload.vue'
import Cashback from './views/Cashback.vue'
import Gamewheel from './views/gamewheel.vue'
import Depositdesimal from './views/Depositdesimal.vue'
Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'main',
      components: {
        Header: Header,
        default: Main,
        Footer: Footer
      }
    }, {
      path: '/Login',
      name: 'login',
      components: {
        Header: Header,
        default: Login
      }
    },
    {
      path: '/Header',
      name: 'header',

      component: Header
    },
    {
      path: '/Register',
      name: 'register',
      components: {
        Header: Header,
        default: Register
      }
    },
    {
      path: '/Redirect/:path',
      name: 'redirect',
      components: {
        Header: Header,
        default: Redirect
      }
    },
    {
      path: '/Logout',
      name: 'logout',
      components: {
        Header: Header,
        default: Logout
      }
    },
    {
      path: '/Deposit',
      name: 'deposit',
      components: {
        Header: Header,
        default: Deposit,
        Footer: Footer
      }
    },
    {
      path: '/Withdraw',
      name: 'withdraw',
      components: {
        Header: Header,
        default: Withdraw, Footer: Footer
      }
    },
    {
      path: '/Bonus',
      name: 'bonus',
      components: {
        Header: Header,
        default: Bonus, Footer: Footer
      }
    },
    {
      path: '/Affiliate',
      name: 'affiliate',
      components: {
        Header: Header,
        default: Affiliate, Footer: Footer
      }
    },
    {
      path: '/History',
      name: 'history',
      components: {
        Header: Header,
        default: History, Footer: Footer
      }
    },
    {
      path: '/Profile',
      name: 'profile',
      components: {
        Header: Header,
        default: Profile, Footer: Footer
      }
    },
    {
      path: '/Footer',
      name: 'footer',
      components: {
        Header: Header,
        default: Footer
      }
    },
    {
      path: '/Otp',
      name: 'otp',
      components: {
        Header: Header,
        default: Otp
      }
    },
    {
      path: '/Dowload',
      name: 'dowload',
      components: {
        Header: Header,
        default: Dowload,
        Footer: Footer
      }
    },
    {
      path: '/Cashback',
      name: 'cashback',
      components: {
        Header: Header,
        default: Cashback,
        Footer: Footer
      }
    }, {
      path: '/Depositdesimal',
      name: 'Depositdesimal',
      components: {
        Header: Header,
        default: Depositdesimal,
        Footer: Footer
      }
    },
    {
      path: '/Gamewheel',
      name: 'gamewheel',
      components: {

        default: Gamewheel,

      }
    }
  ],
  scrollBehavior: (to, from, savedPosition) => {
    if (savedPosition) {
      return savedPosition;
    }
    if (to.hash) {
      return { selector: to.hash };
    }
    return { x: 0, y: 0 };
  }
})
