<template>
  <div class="Otp container pt-3">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-6" v-if="selectStatus === null">
        <div class="text-center py-3 color-main">
          เลือกช่องทางการยืนยันตัวตน
        </div>
        <div class="card card-deposit p-3">
          <div class="row text-center color-main">
            <a class="col-6 text-dark" @click="selectStatus = 'SMS'">
              ผ่าน SMS
              <br />
              <img
                src="/images/otp/sms.png?_=1"
                alt
                width="85px"
                class="py-2"
              />
            </a>
            <a class="col-6 text-dark" @click="selectStatus = 'LINE'">
              ผ่าน LINE
              <br />
              <img
                src="/images/otp/linebot.png?_=1"
                alt
                width="85px"
                class="py-2"
              />
            </a>
          </div>
        </div>
        <div class="row">
          <div class="col-4 py-3">
            <router-link
              to="/logout"
              class="btn-block btn-main btn text-center btn-sm"
              >ย้อนกลับ</router-link
            >
          </div>
        </div>
      </div>
      <div class="col-12 col-md-8 col-lg-6" v-if="selectStatus === 'SMS'">
        <div class="text-center py-3 color-main-css">ยืนยันตัวตนผ่าน SMS</div>
        <div class="card card-deposit px-4 py-3">
          <div class="row color-main">
            <div class="col-12">
              <span v-if="countDown === 60">ส่งรหัส OTP ไปยังเบอร์</span>
              <mdb-icon icon="shield-alt" class="mr-2" v-if="countDown < 60" />
              <span v-if="countDown < 60">กรอกรหัส OTP ที่ได้รับ</span>
              <br />
              <input
                icon="text"
                class="form-control text-left my-2"
                id="amount"
                v-model="phonenumber"
                :maxlength="9"
                required
                v-if="countDown === 60"
              />
              <input
                icon="text"
                class="form-control text-left my-2"
                v-model="phoneOtp"
                placeholder="กรอกรหัส OTP"
                required
                v-if="countDown < 60"
              />
            </div>
            <div class="col-12 my-2">
              <button
                class="btn btn-block btn-info"
                @click="requestOTP()"
                v-if="countDown === 60"
              >
                <span>ขอรหัส OTP</span>
              </button>
              <button
                class="btn btn-block btn-info"
                @click="verifyOTP()"
                v-if="countDown < 60"
              >
                <span>ยืนยัน OTP ({{ countDown }} วินาที)</span>
              </button>
            </div>
            <small>
              <p class="color-main-css px-4">
                หากไม่ได้รับ OTP ผ่าน SMS ที่ส่งไปยังมือถือของคุณ ภายใน 60
                วินาที กรุณาแอดไลน์ เพื่อติดต่อพนักงาน
              </p>
            </small>
          </div>
        </div>
        <a
          class="btn btn-block btn-rounded btn-success my-3 color-main"
          target="_blank"
          :href="$link_linecontact"
        >
          <mdb-icon fab icon="line" class="mx-1" />
          <span>คลิกที่นี่เพื่อแอดไลน์ติดต่อพนักงาน</span>
        </a>
        <div class="row justify-content-center col-5">
          <a
            @click="selectStatus = null"
            class="btn-block btn-main btn text-center btn-sm"
            >ย้อนกลับ</a
          >
        </div>
      </div>
      <div class="col-12 col-md-8 col-lg-6" v-if="selectStatus === 'LINE'">
        <div class="text-center py-3 color-main-css">ยืนยันตัวตนผ่านไลน์</div>
        <div class="card card-deposit px-4 py-3 text-center">
          <div>
            <img :src="$qrcode_linealert" alt width="134px" />
          </div>

          <a
            class="btn btn-block btn-rounded btn-success my-3 color-main"
            target="_blank"
            :href="$line_alert"
          >
            <span>สแกนคิวอาร์โค้ดหรือคลิกเพื่อแอดไลน์</span>
          </a>
          <button
            class="btn btn-block btn-main color-main text-white"
            @click="checkregisterLine()"
          >
            <span>ตรวจสอบการยืนยันตัวตน</span>
          </button>
        </div>
        <div class="row">
          <a @click="detailLine = true" class="col-12 col-md-12 col-lg-12 py-3">
            <div class="btn-block btn btn-danger text-center">
              <span class="p-2 button-border-line-css"
                >ขั้นตอนการยืนยันตัวตน</span
              >
            </div>
          </a>
        </div>
        <mdb-modal
          :show="detailLine"
          @close="detailLine = false"
          scrollable
          centered
        >
          <mdb-modal-body class="text-left card-main">
            <mdb-icon icon="user-plus" class="py-2" />
            <span class="h4 ml-2 color-main">วิธีการสมัครสมาชิก</span>

            <div class="row justify-content-center my-3 color-main">
              <div class="col-11 text-left border-left-css">
                <b>
                  <p>1.คลิกเพื่อแอดไลน์หรือสแกน QR code</p>
                  <p>2.กดเมนูเปิดแจ้งเตือน</p>
                  <p>3.ระบุเบอร์ที่สมัครสมาชิก</p>
                  <p>
                    4.ถ้ามีข้อความตอบกลับ "เปิดใช้งานระบบการแจ้งเตือน
                    และยืนยันตัวตนสำเร็จ" เป็นการยืนยันตัวตนสำเร็จ
                  </p>
                  <p>5.ตรวจสอบการยืนยันตัวตน</p>
                </b>
              </div>
              <div class="col-12 my-2 text-center">
                <button
                  class="btn-main btn text-white"
                  @click="detailLine = false"
                >
                  <span>ย้อนกลับ</span>
                </button>
              </div>
            </div>
          </mdb-modal-body>
        </mdb-modal>
        <div class="row justify-content-center col-5">
          <a
            @click="selectStatus = null"
            class="btn-block btn-main btn text-center btn-sm"
            >ย้อนกลับ</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import { mdbIcon } from "mdbvue";
const jwt = require("jsonwebtoken");
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      countDown: 60,
      phoneOtp: null,
      phonenumber: null,
      selectStatus: null,
      contactline: this.$contactline,
      detailLine: false,
    };
  },
  components: {
    mdbIcon,
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
    countDownTimer() {
      if (this.countDown > 0) {
        setTimeout(() => {
          this.countDown -= 1;
          this.countDownTimer();
        }, 1000);
      } else if (this.countDown === 0) {
        this.countDown = 60;
      }
    },
    verifyOTP() {
      let payload = {
        phone_number: this.phonenumber
          ? this.phonenumber
          : this.user.phone_verify,
        otp: this.phoneOtp,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      $(".showloader").show();
      this.$axios
        .post("/otp_verify", { token: token }, this.token)
        .then((response) => {
          $(".showloader").hide();
          if (response.data.code === "ERROR") {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: "รหัส OTP ไม่ถูกต้อง",
              icon: "error",
              timer: false,
              showConfirmButton: true,
            });
          } else if (response.data.code === "ERROR_PN") {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              html: response.data.msg,
              icon: "error",
              timer: false,
              showConfirmButton: true,
            });
          } else {
            this.$swal({
              title: "สำเร็จ",
              html: response.data.msg,
              icon: "success",
              timer: false,
              showConfirmButton: true,
            });
            this.$axios.get("/is_login", this.token).then((response) => {
              //   $(".preloader").hide();
              if (response.data.msg != "LOGOUT") {
                this.$session.set("isLogin", true);
                this.$session.set("token", response.data);
                this.storeLogin(response.data);
              }
              this.$router.push("/Redirect/Main");
            });
          }
        });
    },
    checkregisterLine() {
      //   $(".preloader").show();
      this.$axios
        .get("/is_login", this.token)
        .then((response) => {
          //   $(".preloader").hide();
          if (response.data.msg != "LOGOUT") {
            this.$session.set("isLogin", true);
            this.$session.set("token", response.data);
            this.storeLogin(response.data);
            if (this.user.active === 0) {
              this.$swal({
                title: "เกิดข้อผิดพลาด",
                text: "คุณยังยืนยันตัวตนไม่สำเร็จ",
                icon: "error",
                timer: 3000,
                showConfirmButton: true,
              });
            } else {
              this.$swal({
                title: "สำเร็จ",
                text: "คุณยืนยันตัวตนสำเร็จ",
                icon: "success",
                timer: 3000,
                showConfirmButton: true,
              });
              this.$router.push("/Redirect/Main");
            }
          } else {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: "มีการเข้าสู่ระบบจากที่อื่น",
              icon: "error",
              timer: 3000,
              showConfirmButton: false,
            });
            this.$router.push("/Logout");
          }
        })
        .catch(function(error) {
          //   $(".preloader").hide();
          console.log(error);
        });
    },
    requestOTP() {
      //   $(".preloader").show();
      let payload = {
        phone_number: this.phonenumber,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      $(".showloader").show();
      this.$axios
        .post("/otp", { token: token }, this.token)
        .then((response) => {
          $(".showloader").hide();
          //   $(".preloader").hide();
          if (response.data.code === "ERROR") {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: response.data.msg,
              icon: "error",
              timer: false,
              showConfirmButton: true,
            });
          } else {
            this.countDownTimer();
            this.$swal({
              title: "สำเร็จ",
              html:
                "ระบบได้ส่งรหัส OTP ไปยัง<br>หมายเลข " +
                this.user.phone_number +
                " เรียบร้อยแล้ว<br>กรุณากรอกรหัส OTP ทีได้รับ",
              icon: "success",
              timer: false,
              showConfirmButton: true,
            });
          }
        })
        .catch(function(error) {
          //   $(".preloader").hide();
          console.log(error);
        });
    },
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  mounted() {
    this.phonenumber = this.user.phone_number;
    $(".showloader").hide();
  },
};
</script>

<style></style>
