<template>
  <div class="Login container">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-4">
        <img src="logoslotxo.png" width="60%" alt />
        <form @submit.prevent="login" class="text-left container">
          <label for="exampleInput">กรอกเบอร์โทรศัพท์</label>
          <input
            type="text"
            id="exampleInput"
            class="form-control"
            v-model="phonenumber"
            maxlength="10"
            onkeypress="return event.charCode >= 48 && event.charCode <= 57"
          />
          <label for="exampleInput" class="mt-1">กรอกรหัสผ่าน</label>
          <!-- <input
            type="password"
            id="exampleInput"
            class="form-control"
            v-model="password"
          /> -->
          <mdb-input
            basic
            class="mb-1"
            v-model="password"
            :type="typepassword"
            style="width: 100%;"
          >
            <div class="row justify-content-end">
              <div class="col-10"></div>
              <a
                class="col-2 text-right"
                style="margin-top: -30px; margin-right: 15px;"
                @click="show_hide()"
              >
                <mdb-icon
                  icon="eye-slash"
                  class="color-toppic"
                  style="color: black;"
                  v-if="typepassword === 'password'"
                />
                <mdb-icon
                  icon="eye"
                  class="color-toppic"
                  style="color: black;"
                  v-else
                />
              </a>
            </div>
            <div class="text-danger" v-if="statuscheckpass === false">
              {{ errorpass }}
            </div>
            <!-- <span
              class="edit-inputgroup"
              style="margin-left:-12px;"
              slot="prepend"
            >
            </span> -->
          </mdb-input>

          <div class="row justify-content-center mt-3">
            <div class="col-8 col-md-8 col-lg-8">
              <mdb-btn
                color="info"
                class="btn-block"
                type="submit"
                rounded
                :disabled="phonenumber == null || password == null"
                v-if="!Isloading"
              >
                <span class="h4">เข้าสู่ระบบ</span>
              </mdb-btn>
              <mdb-btn
                color="info"
                class="btn-block"
                type="submit"
                rounded
                v-else
              >
                <span
                  class="spinner-grow spinner-grow-sm"
                  role="status"
                  aria-hidden="true"
                ></span
                ><span class="h4">เข้าสู่ระบบ</span>
              </mdb-btn>
              <div class="mt-2 text-center">
                <router-link
                  to="/register"
                  href
                  class="text-white h5 border-bottom"
                  >สมัครสมาชิก</router-link
                >
              </div>
            </div>
            <div class="col-12 col-md-8">
              <a
                class="btn btn-block mt-3 btn-success"
                style="box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);border-radius: 8px;"
                target="_blank"
                :href="$link_linecontact"
              >
                <span class="h6">
                  <mdb-icon
                    fab
                    icon="line"
                    class="mr-1"
                  />กดแอดไลน์เพื่อติดต่อพนักงาน
                </span>
              </a>
            </div>
          </div>
        </form>

        <div class="row text-white mt-5">
          <div
            class="col-12  text-center"
            v-for="(games, index) in game"
            :key="index"
          >
            <a
              :href="games.link"
              target="_blank"
              class="text-white"
              rel="noreferrer"
            >
              <img
                :src="games.image"
                class="img-responsive"
                width="40%"
                alt="image"
              />
              <p style="font-size:12px!important">{{ games.title }}</p>
            </a>
          </div>
        </div>
        <center class="mb-2">Copyright © MABUUXO.com All Right Reserved</center>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import momentjs from "moment";
var apiGetIp = "https://api.ipify.org";
import axios from "axios";
var datafootergameRef = firebase.database().ref("game");
var popupRef = firebase.database().ref("popup");
import { mapActions, mapGetters } from "vuex";
const jwt = require("jsonwebtoken");
export default {
  data() {
    return {
      Isloading: false,
      phonenumber: null,
      password: null,
      ip: null,
      game: null,
      typepassword: "password",
      statuscheckpass: true,
    };
  },
  computed: {
    ...mapGetters({
      version: "version",
      token: "token",
    }),
  },
  created() {
    if (this.$route.query.phone_number && this.$route.query.password) {
      this.phonenumber = this.$route.query.phone_number;
      this.password = this.$route.query.password;
      this.login();
    }
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      updateAmount: "updateAmount",
    }),
    login: function() {
      let payload = {
        phone_number: this.phonenumber,
        password: this.password,
        ip: this.ipuser,
      };

      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "6000s",
      });
      this.Isloading = true;
      this.$axios
        .post("/login", { token: token })
        .then((result) => {
          this.Isloading = false;
          if (result.data.code === "ERROR") {
            if (result.data.msg === "NOT_FOUND_PARAMETER") {
              this.$swal({
                title: "เกิดข้อผิดพลาด",
                text: "กรุณากรอกข้อมูลให้ครับ",
                showConfirmButton: true,
                icon: "error",
              });
            } else {
              this.$swal({
                title: "เกิดข้อผิดพลาด",
                text: result.data.msg,
                showConfirmButton: true,
                icon: "error",
              });
            }
          } else {
            // this.$swal({
            //   title: "สำเร็จ",
            //   html: result.data.msg,
            //   timer: 1000,
            //   icon: "success",
            // });
            popupRef.child("main").on("value", (snap) => {
              let queuePopup = [];
              snap.val().forEach((val) => {
                if (
                  val.status === 1 &&
                  momentjs().format("HH:mm") >= val.time_start &&
                  momentjs().format("HH:mm") <= val.time_end
                )
                  queuePopup.push({
                    title: val.title,
                    html: val.text,
                    imageUrl: val.imageUrl,
                    showConfirmButton: true,
                  });
              });
              this.$swal.queue(queuePopup); // showPopup QUEUE
            });
            this.$session.set("version", this.version);
            this.$session.set("isLogin", true);
            this.$session.set("token", result.data);
            this.storeLogin(result.data);
            this.$router.push("/Redirect/Main");
          }
        })
        .catch((err) => {
          console.log(err);
          this.$swal({
            title: "เกิดข้อผิดพลาด",
            html: "ระบบเกิดข้อผิดพลาดกรุณาแจ้งพนักงาน",
            showConfirmButton: true,
            icon: "error",
          });
        });
    },
    show_hide: function() {
      if (this.typepassword === "password") {
        this.typepassword = "text";
      } else {
        this.typepassword = "password";
      }
    },
  },
  mounted() {
    datafootergameRef.on("value", (snap) => {
      this.game = snap.val();
    });
    if (this.$route.query.phone_number && this.$route.query.password) {
      this.phonenumber = this.$route.query.phone_number;
      this.password = this.$route.query.password;
      this.login();
    }
    axios.get(apiGetIp).then((response) => {
      this.ipuser = "" + response.data;
    });
  },
};
</script>

<style>
.Login {
  background: linear-gradient(180deg, #ffe600 0%, #970100 100%) !important;
  min-width: 100vw !important;
  height: 100vh !important;
}
</style>
