<template>
  <div class="Cashback container" style="margin-top:80px;">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-8">
        <mdb-alert color="warning" class="text-left">
          <strong class="font-weight-bold">กติกา CASHBACK </strong>
          <small>
            <p>
              - นับเฉพาะบิลที่ฝากขั่นต่ำ 100 บาท เท่านั้น <br />
              - ตัดยอดเงินเสียทุกๆ24.00นะคะ(จะถูกตัดเหลือ0บาท) <br />
              - ต้องมียอดเงินต่ำกว่า1บาทในบิลนั้นๆ <br />
              - หากมีการฝากก่อนเงินต่ำกว่า 1 บาทจะถือเป็นโมฆะคะ <br />
              - ขอให้ลูกค้าทุกท่านรักษาสิทธิ์ของท่านด้วยเองนะคะ
            </p>
          </small>
        </mdb-alert>
        <div class="text-left" v-if="cashback != 0">
          <span class="h5 text-dark font-weight-bold">ยอดเสียวันนี้</span>
          <span class="ml-2 color-main h3">
            {{ currencyFormat(amount_loss) }}
          </span>
        </div>
        <mdb-card class="mt-3 card-deposit">
          <div class="p-2">
            <div class="row justify-content-center">
              <div class="col-12 col-md-8 col-lg-8 mt-3" v-if="cashback != 0">
                <div>
                  <span class="text-dark font-weight-bold"
                    >CASHBACK ที่ได้รับ</span
                  >
                </div>
                <div>
                  <span class="h3 color-main mb-2 font-weight-bold">{{
                    currencyFormat(cashback)
                  }}</span>
                </div>
              </div>
              <div class="col-12  mt-3 px-2" v-if="cashbackmsg">
                <div class="text-center">
                  <mdb-alert color="danger" role="alert"
                    >*{{ cashbackmsg }}</mdb-alert
                  >
                </div>
              </div>
              <div class="mt-3 col-12">
                <mdb-alert color="danger" v-if="cashbacktitle">{{
                  cashbacktitle
                }}</mdb-alert>
              </div>
            </div>
          </div>
        </mdb-card>
        <div class="row justify-content-center mt-3">
          <div class="col-8 col-md-6 col-lg-6">
            <mdb-btn
              class="btn btn-info btn-block font-weight-bold"
              color
              rounded
              :disabled="cashback < 1"
              @click="takecashback()"
              v-if="!Isloading"
              >รับ CASHBACK</mdb-btn
            >
            <mdb-btn
              v-else
              class="btn btn-info btn-block font-weight-bold"
              color
              rounded
              ><span
                class="spinner-grow spinner-grow-sm"
                role="status"
                aria-hidden="true"
              ></span
              >รับ CASHBACK</mdb-btn
            >
          </div>
        </div>
        <div class="py-3 text-left">
          <router-link to="/" class="btn btn-main btn-sm">ย้อนกลับ</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      cashback: 0,
      amount_loss: 0,
      cashbackmsg: null,
      cashbacktitleReceive: null,
      cashbacktitle: null,
    };
  },
  computed: {
    ...mapGetters({
      version: "version",
      token: "token",
      Isloading: false,
    }),
  },
  methods: {
    currencyFormat(n) {
      n = parseFloat(n);
      if (isNaN(n)) {
        return "0.00";
      } else {
        return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
      }
    },
    takecashback() {
      this.Isloading = true;
      $(".showloader").show();
      this.$axios
        .get("/takeCashback", this.token)
        .then((result) => {
          this.Isloading = false;
          $(".showloader").hide();
          if (result.data.code === "SUCCESS") {
            this.$swal({
              title: "สำเร็จ",
              html: "คุณได้รับ Cashback จำนวน : " + this.cashback.toFixed(2),
              type: "success",
              allowEscapeKey: false,
              allowOutsideClick: false,
            });
            this.$axios
              .get("/cashback", this.token)
              .then((result) => {
                $(".showloader").hide();
                this.cashback = result.data.payload.cashback;
                this.amount_loss = result.data.payload.amount;
                this.cashbackmsg = result.data.msg;
                this.cashbacktitle = "";
              })
              .catch((err) => {
                console.log(err);
                $(".showloader").hide();
                this.$router.push("/");
              });
          } else {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              html: result.data.msg,
              icon: "warning",
            });
          }
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
    },
  },
  mounted() {
    if (this.token) {
      $(".showloader").show();
      this.$axios
        .get("/cashback", this.token)
        .then((result) => {
          $(".showloader").hide();
          if (result.data.code !== "ERROR") {
            this.cashback = result.data.payload.cashback;
            this.amount_loss = result.data.payload.amount;
            this.cashbackmsg = result.data.msg;
            this.cashbacktitleReceive = result.data.payload.title;
          } else {
            this.cashbacktitle = result.data.msg;
          }
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
    }
  },
};
</script>

<style></style>
