                  />
                </div>
                <div class="form-group">
                  <label for="example1 white-text" color="red">
                    <span class="color-main">กรอกจำนวนเงิน</span>
                  </label>
                  <input
                    type="text"
                    id="example1"
                    class="form-control color-main bg-input-css"
