                    v-mask="{
                      alias: 'currency',
                      prefix: '',
                      groupSeparator: '',
                    }"
                    maxlength="8"
                  />
                </div>
                <div class="text-center">
                  <button
                    class="btn btn-block btn-main text-white mb-2"
