                  </button>
                  <button
                    v-else
                    class="btn btn-block btn-main text-white mb-2"
                    style="margin-left: 0px"
                  >
                    <span
                      class="spinner-grow spinner-grow-sm"
                      role="status"
                      aria-hidden="true"
                    ></span>
                    <span class="h6">ยืนยันยอดฝาก</span>
                  </button>
                  <a @click="submitTrue = false" class="text-center mt-3"
                    ><span class="color-main">ยกเลิก</span></a
                  >
                </div>
              </div>
            </div>
            <a
              class="btn btn-sm btn-block btn-success mt-3"
              target="_blank"
