            >
              <div class="my-2 h6">ระบบแจ้งเตือนรายการฝาก-ถอน ผ่าน LINE</div>
            </a>
          </div>
        </div>
        <small>
          <p class="text-dark pt-3">
            *กดเพิ่มเพื่อนเพื่อรับการแจ้งเตือนยอดฝาก
            <br />**หลังจากโอนเงินแล้ว ระบบจะใช้เวลาดำเนินการไม่เกิน 1 นาที
            แต่หากเกินเวลาที่กำหนด ให้ติดต่อพนักงานทันที
