              - สมาชิก จะต้องมียอดเครดิตคงเหลือในระบบต่ำกว่า 10 บาท มิเช่นนั้น
              ระบบจะนับรวมยอดที่ต้องทำรวมกับยอดก่อนหน้านี้
            </span>
          </small>
        </mdb-alert>
        <mdb-alert color="warning" class="text-left">
          <center>
            <span class="font-weight-bold">คำเตือน</span>
          </center>
          <small>
            <p>
              - ก่อนทำรายการฝาก
              รบกวนสมาชิกเช็คหมายเลขบัญชีธนาคารที่หน้าระบบทุกครั้ง
            </p>
            <p>
              -
              สมาชิกต้องโอนเงินผ่านทางแอพพลิเคชั่นของธนาคารและต้องใช้บัญชีเดียวกันกับที่สมัครเข้ามาเท่านั้น
            </p>

            <p>
              - กรณีที่สมาชิกทำรายการฝากเงินผ่านตู้ ATM
              จะทำให้ยอดเงินของท่านไม่เข้าสู่ในระบบ
            </p>
            <p>
              - สมาชิกที่ต้องการเล่น "ONLINE GAMING" จะต้องไม่รับโบนัส
              ถึงจะสามารถเข้าเล่นได้ *ถ้าเกิดข้อผิดพลาดของระบบ
              ให้แจ้งกับพนักงานทันที กรณีที่ไม่แจ้งเราขอสงวนสิทธิ์การถอนเงิน
              ทุกกรณี
            </p>
          </small>
        </mdb-alert>
        <div class="text-left py-3">
          <a @click="selectstatus = 'DEPOSIT'" class="btn btn-sm btn-main"
            >ย้อนกลับ</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import momentjs from "moment";
import firebase from "firebase";
var popupRef = firebase.database().ref("popup");
const jwt = require("jsonwebtoken");
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      Isloading: false,
      selectstatus: "DEPOSIT",
      deposit: null,
      bonus_config: null,
      wallet: null,
      switchValue: true,
      submitTrue: false,
      phoneWallet: null,
      amountWallet: null,
      errors: [],
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    bankNumberFormat(str) {
      return str.replace(/[0-9]/g, (x, index) => {
        if (index == 2 || index == 5) return x + "-";
        return x;
      });
    },
    setBonus: function() {
      let payload = {
        is_bonus: this.switchValue === false ? 1 : 0,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      $(".showloader").show();
      this.$axios
        .post("/is_bonus", { token: token }, this.token)
        .then((response) => {
          if (response.data.code === "SUCCESS") {
            this.$axios
              .get("/is_login", this.token)
              .then((response) => {
                if (response.data.msg != "LOGOUT") {
                  this.$session.set("isLogin", true);
                  this.$session.set("token", response.data);
                  this.storeLogin(response.data);
                  this.$axios
                    .get("/bonus", this.token)
                    .then((response) => {
                      $(".showloader").hide();
                      this.bonus_config = response.data.payload;
                    })
                    .catch(function(error) {
                      console.log(error);
                    });
                } else {
                  $(".showloader").hide();
                  this.$swal({
                    title: "เกิดข้อผิดพลาด",
                    text: "มีการเข้าสู่ระบบจากที่อื่น",
                    type: "error",
                    timer: 3000,
                    showConfirmButton: false,
                  });
                  this.$router.push("/logout");
                }
              })
              .catch(function(error) {
                console.log(error);
              });
          } else {
            $(".showloader").hide();
            console.log("false");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    check_condition: function() {
      this.errors = [];
      // Check phonenumber

      if (this.phoneWallet !== null) {
        if (this.phoneWallet.length !== 10) {
          this.errors.push("กรุณากรอกเบอร์โทรศัพท์ให้ครบ 10 หลัก");
          return this.$swal({
            title: "เบอร์โทรศัพท์",
            text: "กรุณากรอกเบอร์โทรศัพท์ให้ครบ 10 หลัก",
            icon: "warning", //error warning success
            timer: 5000,
            showConfirmButton: true,
            allowOutsideClick: false,
            allowEscapeKey: false,
          });
        } else if (this.phoneWallet[0] !== "0") {
          this.errors.push("เบอร์โทรศัพท์ต้องขึ้นต้นด้วย 0 เท่านั้น");
          return this.$swal({
            title: "เบอร์โทรศัพท์",
            text: "เบอร์โทรศัพท์ต้องขึ้นต้นด้วย 0 เท่านั้น",
            icon: "warning",
            timer: 5000,
            showConfirmButton: true,
            allowOutsideClick: false,
            allowEscapeKey: false,
          });
        } else if (this.phoneWallet[0] === "0") {
          if (
            this.phoneWallet[1] === "8" ||
            this.phoneWallet[1] === "9" ||
            this.phoneWallet[1] === "6"
          ) {
            //  this.$router.push("/");
          } else {
            this.errors.push(
              "เบอร์โทรศัพท์ต้องขึ้นต้นด้วย 06 หรือ 08 หรือ 09 เท่านั้น"
            );
            return this.$swal({
              title: "เบอร์โทรศัพท์",
              text: "เบอร์โทรศัพท์ต้องขึ้นต้นด้วย 06 หรือ 08 หรือ 09 เท่านั้น",
              icon: "warning",
              timer: 5000,
              showConfirmButton: true,
              allowOutsideClick: false,
              allowEscapeKey: false,
            });
          }
        }
        if (parseFloat(this.amountWallet) < 1) {
          this.errors.push("จำนวนเงินต้องมากกว่า 1 บาท");
          return this.$swal({
            title: "จำนวนเงิน",
            text: "จำนวนเงินต้องมากกว่า 1 บาท",
            icon: "warning",
            timer: 5000,
            showConfirmButton: true,
            allowOutsideClick: false,
            allowEscapeKey: false,
          });
        }
      } else {
        this.$swal({
          title: "เกิดข้อผิดพลาด",
          text: "กรุณากรอกเบอร์โทรศัพท์ให้ครบ 10 หลัก",
          icon: "warning", //error warning success
          timer: 5000,
          showConfirmButton: true,
          allowOutsideClick: false,
          allowEscapeKey: false,
        });
      }
    },
    deposittrue() {
      this.check_condition();
      if (this.errors.length <= 0) {
        let payload = {
          bank_id: this.wallet[0].ID,
          phone_number: this.phoneWallet,
          amount: this.amountWallet,
        };
        let token = jwt.sign(payload, this.$keypayload, {
          expiresIn: "300s",
        });
        this.Isloading = true;
        $(".preloader").show();
        this.$axios
          .post("/depositTruewallet", { token: token }, this.token)
          .then((result) => {
            this.Isloading = false;
            $(".preloader").hide();
            if (result.data.code === "ERROR") {
              this.$swal({
                title: "เกิดข้อผิดพลาด",
                html: result.data.msg,
                icon: "error",
                // timer: 5000,
                showConfirmButton: true,
                allowOutsideClick: false,
                allowEscapeKey: false,
              });
              this.bonusWdf = null;
              this.phoneWallet = "";
              this.amountWallet = 0;
            } else {
              let payload = {
                username: this.user.username,
                current: this.amountWallet,
              };
              let token = jwt.sign(payload, this.$keypayload, {
                expiresIn: "300s",
              });
              this.$axios
                .post("/affiliateDeposit", { token: token }, this.token)
                .then((result) => {
                  return result;
                });
              this.$swal({
                title: "สำเร็จ",
                html: result.data.msg,
                icon: "success",
                timer: 5000,
                showConfirmButton: true,
                allowOutsideClick: false,
                allowEscapeKey: false,
              }).then((result) => {
                if (result.value) {
                  this.$router.push("/");
                }
              });
            }
          });
      }
    },
    onCopy: function(e) {
      this.$swal({
        title: "คัดลอกเลขบัญชี",
        text: "คัดลอกเลขบัญชี : " + e.text,
        icon: "success",
        allowEscapeKey: false,
        allowOutsideClick: false,
        showConfirmButton: true,
      });
    },
  },
  mounted() {
    if (this.isLogin) {
      $(".showloader").show();
      if (this.amount.code === "ERROR") {
        $(".showloader").hide();
        this.$swal({
          title: "ระบบปิดปรับปรุง",
          text: "กรุณารอซักครู่...",
          type: "warning",
          allowEscapeKey: false,
          allowOutsideClick: false,
          showConfirmButton: true,
        }).then((result) => {
          if (result.value) {
            this.$router.push("/Redirect/Main");
          }
        });
      } else {
        popupRef.child("deposit").on("value", (snap) => {
          let queuePopup = [];
          snap.val().forEach((val) => {
            if (
              val.status === 1 &&
              momentjs().format("HH:mm") >= val.time_start &&
              momentjs().format("HH:mm") <= val.time_end
            )
              queuePopup.push({
                title: val.title,
                html: val.text,
                imageUrl: val.imageUrl,
                showConfirmButton: true,
              });
          });
          this.$swal.queue(queuePopup); // showPopup QUEUE
        });

        this.switchValue = this.user.is_bonus === 1 ? true : false;
        $(".showloader").show();
        this.$axios
          .get("/showbank", this.token)
          .then((result) => {
            if (result.data.code != "ERROR") {
              this.deposit = result.data;
              this.$axios
                .get("/bonus", this.token)
                .then((result) => {
                  $(".showloader").hide();
                  this.bonus_config = result.data.payload;
                })
                .catch(function(error) {
                  console.log(error);
                  $(".showloader").hide();
                });
              this.$axios
                .get("/showBank?bank=TRUEWALLET", this.token)
                .then((result) => {
                  $(".showloader").hide();
                  if (result.data.code != "ERROR") {
                    this.wallet = result.data;
                  } else {
                    this.wallet = null;
                  }
                })
                .catch(function(error) {
                  $(".showloader").hide();
                  console.log(error);
                });
            } else {
              $(".showloader").hide();
              this.deposit = null;
            }
          })
          .catch(function(error) {
            console.log(error);
            $(".showloader").hide();
          });
      }
    } else {
      this.$router.push("/Logout");
    }
  },
};
</script>

