<template>
  <div class="Withdraw container pt-3" style="margin-top: 80px">
    <div class="row justify-content-center">
      <div
        class="col-12 col-md-8 col-lg-6 text-center"
        v-if="linkAffiliate === null"
      >
        <div class="btn btn-main text-white" @click="registerAffiliate()">
          <span>ขอลิ้งแนะนำเพื่อน</span>
        </div>
      </div>
      <div class="col-12 col-md-8 col-lg-8 text-left" v-if="affiliate">
        <label for class="text-dark font-weight-bold" v-if="affiliate.join">
          รหัสผู้แนะนำ :
          <span class="color-main">{{ affiliate.join }}</span>
        </label>

        <mdb-card class="mt-3 card-deposit">
          <div class="p-2 text-center">
            <span class="h5 font-weight-bold color-toppic">บัญชีของคุณ</span>
            <div class="row justify-content-center">
              <div class="col-12 col-md-8 col-lg-8 mt-3 pb-3">
                <div class="row justify-content-center" v-if="user">
                  <div class="col-4 col-lg-6 align-self-centertext-right">
                    <img
                      :src="'/images/bank/' + user.bank_code + '.png'"
                      width="80%"
                      alt
                    />
                  </div>
                  <div
                    class="col-7 col-lg-6 text-dark text-left font-weight-bold align-self-center"
                  >
                    {{ user.bank_type }}
                    <br />
                    {{ bankNumberFormat(user.bank_number) }}

                    <br />
                    {{ user.full_name }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </mdb-card>
        <div class="py-1 text-center h5" v-if="walletData">
          <label for class="text-dark mt-2">รายได้จากการแนะนำเพื่อน</label>
          <br />
          <label for class="color-main h2">{{
            currencyFormat(walletData.walletSum)
          }}</label>
          <a @click="depositWallet()">
            <mdb-icon icon="redo-alt" class="text-dark ml-1" />
          </a>
          <div class="row justify-content-center">
            <div class="col-8">
              <mdb-btn
                color="info"
                class="btn-block"
                tag="a"
                @click="withdraw()"
                :disabled="walletData.walletSum === 0"
                v-if="!Isloading"
                rounded
              >
                <span class="h4">กดรับรายได้</span>
              </mdb-btn>
              <mdb-btn color="info" class="btn-block" tag="a" rounded v-else>
                <span
                  class="spinner-grow spinner-grow-sm"
                  role="status"
                  aria-hidden="true"
                ></span>
                <span class="h4">กดรับรายได้</span>
              </mdb-btn>
            </div>
          </div>
          <div class="text-left mt-3">
            <label for class="color-main h6">ลิ้งก์แนะนำเพื่อนของคุณ</label>
            <div class="input-group mb-3">
              <input
                type="text"
                class="form-control bg-input-register"
                :placeholder="linkAffiliate"
                aria-label="Recipient's username"
                aria-describedby="basic-addon2"
              />
              <div
                class="input-group-append bg-submit-aff"
                style="margin-left: -10px; z-index: 1"
              >
                <a
                  v-clipboard:copy="linkAffiliate"
                  v-clipboard:success="onCopy"
                >
                  <span
                    class="input-group-text bg-submit-aff text-white"
                    id="basic-addon2"
                    >คัดลอก</span
                  ></a
                >
              </div>
            </div>
            <label for class="color-main h6"
              >ประวัติการทำรายการ 20 รายการล่าสุด</label
            >
            <mdb-card class="card-deposit mb-3">
              <div class="p-2 text-center">
                <mdb-scrollbar class="Maxheight">
                  <mdb-tbl borderless>
                    <mdb-tbl-head color="light">
                      <tr>
                        <th scope="col">
                          <span class="color-main">
                            <small class="font-weight-bold">#</small>
                          </span>
                        </th>
                        <th scope="col">
                          <span class="color-main">
                            <small class="font-weight-bold">ว/ด/ป เวลา</small>
                          </span>
                        </th>
                        <th scope="col">
                          <span class="color-main">
                            <small class="font-weight-bold">จำนวนเงิน</small>
                          </span>
                        </th>
                      </tr>
                    </mdb-tbl-head>

                    <mdb-tbl-body v-if="walletData">
                      <tr
                        v-for="(val, index) in walletData.payload"
                        :key="index"
                        class="text-center"
                      >
                        <td class="text-center py-1 text-dark">
                          {{ index + 1 }}
                        </td>
                        <td class="text-center py-1 text-dark">
                          {{ val.date | moment("DD/MM/Y") }}
                          {{ val.time | moment("HH:mm") }}
                        </td>

                        <td class="text-right py-1">
                          <div
                            class="text-success text-center"
                            v-if="val.amount > 0"
                          >
                            +{{ val.amount }}
                          </div>
                          <div class="text-danger text-center" v-else>
                            {{ val.amount }}
                          </div>
                        </td>
                      </tr>
                    </mdb-tbl-body>
                  </mdb-tbl>
                </mdb-scrollbar>
              </div>
            </mdb-card>
            <label for class="color-main h6">สถิติ</label>

            <mdb-card class="card-deposit">
              <div class="p-2 text-center">
                <mdb-scrollbar class="Maxheight">
                  <mdb-tbl borderless>
                    <mdb-tbl-head color="light">
                      <tr>
                        <th scope="col">
                          <span class="color-main font-weight-bold">
                            <small class="font-weight-bold"
                              >วันที่เข้าร่วม</small
                            >
                          </span>
                        </th>
                        <th scope="col">
                          <span class="color-main font-weight-bold">
                            <small class="font-weight-bold">ยอดแชร์</small>
                          </span>
                        </th>
                        <th scope="col">
                          <span class="color-main font-weight-bold">
                            <small class="font-weight-bold">จำนวนคลิก</small>
                          </span>
                        </th>
                      </tr>
                    </mdb-tbl-head>

                    <mdb-tbl-body>
                      <tr v-if="affiliate">
                        <td>{{ affiliate.joinDate | moment("DD/MM/YYYY") }}</td>
                        <td>{{ affiliate.share }}</td>
                        <td>{{ affiliate.click }}</td>
                      </tr>
                    </mdb-tbl-body>
                  </mdb-tbl>
                </mdb-scrollbar>
              </div>
            </mdb-card>
            <label for class="color-main h6 mt-3">สมาชิกที่คุณแนะนำ</label>

            <div class="row py-2">
              <div class="col-5 color-main">
                <small> เลือกวันที่แสดง</small>
              </div>
              <div class="col-7">
                <date-picker
                  valueType="format"
                  v-model="date"
                  @change="selectDate()"
                  style="width: 100%"
                ></date-picker>
              </div>
            </div>

            <mdb-card class="card-deposit">
              <div class="p-2 text-center">
                <mdb-scrollbar class="Maxheight">
                  <mdb-tbl borderless>
                    <mdb-tbl-head color="light">
                      <tr>
                        <th scope="col">
                          <span class="color-main">
                            <small class="font-weight-bold">#</small>
                          </span>
                        </th>
                        <th scope="col">
                          <span class="color-main font-weight-bold">
                            <small class="font-weight-bold"
                              >วันที่เข้าร่วม</small
                            >
                          </span>
                        </th>
                        <th scope="col">
                          <span class="color-main font-weight-bold">
                            <small class="font-weight-bold">USERNAME</small>
                          </span>
                        </th>
                        <th scope="col">
                          <span class="color-main font-weight-bold">
                            <small class="font-weight-bold">ยอดเสีย 5%</small>
                          </span>
                        </th>
                      </tr>
                    </mdb-tbl-head>

                    <mdb-tbl-body v-if="affiliateData">
                      <tr v-for="(logaff, key) in afflog" :key="key">
                        <td class="text-center text-black">
                          <small>{{ key + 1 }}</small>
                        </td>
                        <td class="text-center text-black">
                          <small>{{
                            affiliateData.payload[0].date
                              | moment(" YYYY-MM-DD")
                          }}</small>
                        </td>
                        <td class="text-center text-black">
                          <small>{{ logaff.username }}</small>
                        </td>
                        <td class="text-center text-black">
                          <small>{{ currencyFormat(logaff.winloss) }}</small>
                        </td>
                      </tr>
                    </mdb-tbl-body>
                  </mdb-tbl>
                </mdb-scrollbar>
              </div>
            </mdb-card>
          </div>
        </div>
        <div class="py-3">
          <router-link to="/" class="btn btn-main btn-sm">ย้อนกลับ</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
import firebase from "firebase";
import momentjs from "moment";
var popupRef = firebase.database().ref("popup");

import $ from "jquery";
const jwt = require("jsonwebtoken");
import { mapGetters, mapActions } from "vuex";
export default {
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    bankNumberFormat(str) {
      return str.replace(/[0-9]/g, (x, index) => {
        if (index == 2 || index == 5) return x + "-";
        return x;
      });
    },
    onCopy: function(e) {
      this.$swal({
        icon: "success",
        title: "สำเร็จ",
        text: "คัดลอกลิ้งแนะนำเพื่อน : " + e.text,
      });
    },
    depositWallet() {
      //   $(".preloader").show();
      let payload = {
        username: this.user.username,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      $(".showloader").show();
      this.$axios
        .post("/walletDeposit", { token: token }, this.token)
        .then((result) => {
          $(".showloader").hide();
          if (result.data.code === "SUCCESS") {
            this.$swal({
              icon: "success",
              title: "สำเร็จ",
              text: "รับเงินเข้าสู่ระบบกระเป๋าตังเรียบร้อยแล้ว",
            });
            this.$axios.get("/wallet", this.token).then((result) => {
              this.wallet = result.data.walletSum;
              //   $(".preloader").hide();
            });
          } else {
            // $(".preloader").hide();
            this.$swal({
              icon: "info",
              title: "แจ้งเตือน",
              text: "ยอดเงินอัพเดทเป็นล่าสุดแล้ว",
            });
          }
        })
        .catch((err) => {
          $(".showloader").hide();
          // $(".preloader").hide();
          console.log(err);
        });
    },
    withdraw() {
      if (this.walletData.walletSum >= 300) {
        this.$swal({
          title: "ต้องการถอนเงิน ?",
          text: "การถอนเงินจะถอนยอดเงินคงเหลือทั้งหมด",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "ตกลง",
          cancelButtonText: "ยกเลิก",
        }).then((result) => {
          if (result.value) {
            // $(".preloader").show();
            let payload = {
              username: this.user.username,
            };
            let token = jwt.sign(payload, this.$keypayload, {
              expiresIn: "300s",
            });
            this.Isloading = true;
            $(".showloader").show();
            this.$axios
              .post("/walletwithdraw", { token: token }, this.token)
              .then((result) => {
                this.Isloading = false;
                $(".showloader").hide();
                if (result.data.code === "SUCCESS") {
                  this.$swal("สำเร็จ!", "แจ้งถอนเงินเรียบร้อยแล้ว", "success");
                } else {
                  this.$swal({
                    title: "เกิดข้อผิดพลาด",
                    text: "ถอนเงินไม่สำเร็จโปรดลองอีกครั้ง",
                    icon: "danger",
                    showCancelButton: true,
                  });
                }
                this.$axios.get("/wallet", this.token).then((result) => {
                  this.history = result.data.payload;

                  this.walletData = result.data;
                  //   $(".preloader").hide();
                });
              });
          }
        });
      } else {
        this.$swal({
          title: "ไม่สามารถถอนได้",
          text: "เนื่องจากคุณต้องมีเงินมากกว่า 300 บาท",
          icon: "info",
          timer: false,
          showConfirmButton: true,
        });
      }
    },
    registerAffiliate() {
      $(".showloader").show();
      let payload = {
        join: undefined,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      this.$axios
        .post("/affiliateRegister", { token: token }, this.token)
        .then((result) => {
          if (result.data.code === "SUCCESS") {
            this.$axios
              .get("/affiliateToken", this.token)
              .then((result) => {
                $(".showloader").hide();
                if (result.data) {
                  this.affiliate = result.data.payload;
                  if (result.data.payload.token) {
                    this.linkAffiliate =
                      this.$linkaff +
                      "affiliateLink?join=" +
                      result.data.payload.token;
                  } else {
                    console.log("else");
                    this.linkAffiliate = null;
                  }
                } else {
                  this.linkAffiliate = null;
                }
              })
              .catch((err) => {
                console.log(err);
              });
          } else {
            $(".showloader").hide();
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: result.data.msg,
              icon: "error",
            });
          }
        });
    },
    selectDate() {
      let payload = {
        date: this.date,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      this.$axios
        .post("/affiliateLog", { token: token }, this.token)
        .then((result) => {
          $(".showloader").hide();
          this.affiliateData = result.data;
          this.afflog = JSON.parse(result.data.payload[0].stat);
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
    },
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  data() {
    return {
      affiliate: null,
      linkAffiliate: null,
      affiliateData: null,
      walletData: null,
      afflog: null,
      date: new Date(),
      Isloading: false,
    };
  },
  components: { DatePicker },
  mounted() {
    if (this.isLogin) {
      popupRef.child("affiliate").on("value", (snap) => {
        let queuePopup = [];
        snap.val().forEach((val) => {
          if (
            val.status === 1 &&
            momentjs().format("HH:mm") >= val.time_start &&
            momentjs().format("HH:mm") <= val.time_end
          )
            queuePopup.push({
              title: val.title,
              html: val.text,
              imageUrl: val.imageUrl,
              showConfirmButton: true,
            });
        });
        this.$swal.queue(queuePopup); // showPopup QUEUE
      });
      $(".showloader").show();
      this.$axios
        .get("/affiliateToken", this.token)
        .then((result) => {
          if (result.data) {
            this.affiliate = result.data.payload;
            if (this.affiliate) {
              this.linkAffiliate =
                "https://abatopup.s3-ap-southeast-1.amazonaws.com/aff/aff.html?service=mabuuxo&&token=" +
                result.data.payload.token +
                "&&path=affiliateLink";
              // this.$linkaff +
              // "affiliateLink?join=" +
              // result.data.payload.token;
            } else {
              console.log("no linkAff");
              this.linkAffiliate = null;
            }
          } else {
            console.log("no tokenaff");
            this.affiliate = null;
          }
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });

      var date_log = this.$momentjs().format("Y-MM-DD");

      let payload = {
        date: date_log,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      this.$axios
        .post("/affiliateLog", { token: token }, this.token)
        .then((result) => {
          $(".showloader").hide();
          this.affiliateData = result.data;
          this.afflog = JSON.parse(result.data.payload[0].stat);
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
      this.$axios
        .get("/wallet", this.token)
        .then((result) => {
          $(".showloader").hide();
          this.walletData = result.data;
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });

      // this.$axios.get("/credit_system_join", this.token).then(result => {
      //   $(".showloader").hide();
      //   this.memberData = result.data.payload;
      // });
      // this.$axios.get("/bonus/201", this.token).then(result => {
      //   $(".showloader").hide();
      //   this.bonusData = result.data;
      // });
    } else {
      this.$router.push("/logout");
    }
  },
};
</script>

<style></style>
