<template>
  <div class="Withdraw container" style="margin-top: 80px">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-8">
        <mdb-card class="mt-3 card-deposit">
          <div class="p-2">
            <span class="h5 font-weight-bold color-toppic">บัญชีของคุณ</span>
            <div class="row justify-content-center">
              <div class="col-12 col-md-8 col-lg-8 mt-3">
                <div class="row justify-content-center container">
                  <div class="col-5 col-lg-6 align-self-center">
                    <img
                      :src="'/images/bank/' + user.bank_code + '.png'"
                      width="80%"
                      alt
                    />
                  </div>
                  <div
                    class="col-7 col-lg-6 text-dark text-left font-weight-bold align-self-center"
                  >
                    {{ user.bank_type }}
                    <br />
                    {{ bankNumberFormat(user.bank_number) }}

                    <br />
                    {{ user.full_name }}
                  </div>
                  <div class="col-12 mt-3">
                    <span class="h5 color-toppic font-weight-bold"
                      >จำนวนเงินคงเหลือ</span
                    >
                    <br />
                    <span class="h3 color-main font-weight-bold"
                      >{{ currencyFormat(amount) }} บาท</span
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </mdb-card>
        <div v-if="amount < 100" class="text-danger font-weight-bold mt-3 h5">
          <span>"ถอนเงินขั้นต่ำ 100 บาท ยอดเงินของคุณไม่พอถอน"</span>
        </div>
        <div v-else-if="withdraw_table && withdraw_table.length > 0">
          <div v-for="(val, index) in withdraw_table" :key="index">
            <p class="alert alert-warning text-danger" role="alert">
              คุณมีรายการแจ้งถอนจำนวน
              <strong>{{ val.amount }} บาท</strong>
              กำลังรอตรวจสอบอยู่ค่ะ
            </p>
          </div>
        </div>
        <div
          class="text-danger pt-3"
          v-else-if="parseFloat(amount) < withdraw_fix"
        >
          <p class="h5 py-2 font-weight-bold">
            คุณต้องมียอดคงเหลือมากกว่าหรือเท่ากับ
            <strong>{{ currencyFormat(withdraw_fix) }} บาท</strong>
            ถึงจะสามารถถอนได้
          </p>
        </div>
        <div v-else>
          <span class="text-white">กรอกจำนวนเงินที่ต้องการถอน</span>
          <!-- <div class="py-2">
            <input
              type="text"
              class="form-control text-right bg-input-register"
              id="withdraw_amount"
              :maxlength="9"
              @change="changeAmount()"
              @blur="updateAmount()"
              @focus="updateAmount()"
              :value="withdraw_amount"
              required
              v-mask="{
                alias: 'currency',
                prefix: '',
                groupSeparator: '',
              }"
            />
          </div> -->
          <div class="col-12 my-1">
            <mdb-btn
              color="info"
              class="btn-block"
              tag="a"
              rounded
              @click="withdraw()"
              id="WITHDRAW"
            >
              <span class="h4">ถอนเงิน</span>
            </mdb-btn>
          </div>
        </div>
        <mdb-alert color="warning " class="text-left mt-3">
          <center>
            <span class="font-weight-bold">กติกาการถอนเงิน</span>
          </center>
          <small>
            <p>- ถอนขั้นต่ำ 100 บาท</p>
            <p>
              - กรณีที่สมาชิกไม่รับโปรโมชั่นใดๆจะต้องมีรายการเล่นอย่างน้อย 1
              ครั้ง
            </p>

            <p>- กรณีที่มีการทุจริต การตัดสินใจของพนักงานถือเป็นที่สิ้นสุด</p>
            <p>
              - สมาชิกที่ฝาก 100 บาท ขึ้นไปสามารถถอนเงินได้ สูงสุด 100 เท่า
              นับจากยอดฝาก เช่น ยอดฝาก 10,000 บาท ถอนได้สูงสุด 1,000,000 บาท
              เป็นต้น
            </p>
          </small>
        </mdb-alert>
        <mdb-alert color="danger" class="mt-3">
          <center>
            <span class="font-weight-bold">คำเตือน</span>
          </center>
          <small class="text-left">
            <p>- กรณีที่เกิดข้อผิดพลาดของระบบให้แจ้งกับพนักงานทันที</p>
            <p>
              -
              กรณีที่สมาชิกไม่แจ้งพนักงานทางเราขอสงวนสิทธิ์ทำรายการถอนเงินให้ทุกกรณี
            </p>
            <p>
              - เมื่อสมาชิกได้รับ Jackpot , Powerbar (ที่ไม่ได้มาจากการเล่นเกม)
              * จะต้องทำยอด 2 เท่า ของยอดแจ็คพอตที่ได้รับ
              จึงจะสามารถทำรายการถอนเงินต้นได้
            </p>

            <p>
              - แจ็คพ็อตที่สมาชิกได้รับจะคิดการทำยอดแยกต่างหาก
              จากการฝากแบบรับโบนัส และฝากแบบไม่รับโบนัส
            </p>
          </small>
        </mdb-alert>
        <div class="text-left">
          <router-link to="/Redirect/Main" class="btn btn-sm btn-main"
            >ย้อนกลับ</router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import momentjs from "moment";
var popupRef = firebase.database().ref("popup");
import $ from "jquery";
const jwt = require("jsonwebtoken");
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      withdraw_fix: null,
      withdraw_table: null,
      withdraw_content: null,
      withdraw_amount: 0.0,
      btndisable: true,
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  methods: {
    ...mapActions({
      updateAmount: "updateAmount",
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    bankNumberFormat(str) {
      return str.replace(/[0-9]/g, (x, index) => {
        if (index == 2 || index == 5) return x + "-";
        return x;
      });
    },
    changeAmount() {
      this.withdraw_amount = $("#withdraw_amount").val();
      if (parseFloat(this.withdraw_amount < 100)) {
        this.btndisable = true;
      } else if (parseFloat(this.amount) < parseFloat(this.withdraw_amount)) {
        this.btndisable = true;
      } else {
        this.btndisable = false;
      }
    },
    withdraw() {
      this.updateAmount();
      let payload = {
        amount: this.amount,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      $(".showloader").show();
      this.$axios
        .post("/withdraw", { token: token }, this.token)
        .then((result) => {
          this.updateAmount(result.data.amount);
          let payload = {
            currency: this.withdraw_amount,
          };
          let token = jwt.sign(payload, this.$keypayload, {
            expiresIn: "300s",
          });
          this.$axios.post("/affiliateWithdraw", { token: token }, this.token);
          {
            if (result.data.code === "SUCCESS") {
              $(".showloader").hide();
              this.$swal({
                title: "สำเร็จ",
                text: result.data.msg,
                icon: "success",
                timer: false,
                showConfirmButton: true,
              }).then((result) => {
                if (result.value) {
                  this.$router.push("/");
                }
              });
            } else {
              $(".showloader").hide();
              this.$swal({
                title: "เกิดข้อผิดพลาด",
                text: result.data.msg,
                icon: "error",
                timer: false,
                showConfirmButton: false,
              });
            }
          }
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
    },
  },
  mounted() {
    if (this.isLogin) {
      $(".showloader").show();
      popupRef.child("withdraw").on("value", (snap) => {
        let queuePopup = [];
        snap.val().forEach((val) => {
          if (
            val.status === 1 &&
            momentjs().format("HH:mm") >= val.time_start &&
            momentjs().format("HH:mm") <= val.time_end
          )
            queuePopup.push({
              title: val.title,
              html: val.text,
              imageUrl: val.imageUrl,
              showConfirmButton: true,
            });
        });
        this.$swal.queue(queuePopup); // showPopup QUEUE
      });
      this.$axios
        .get("/turnTotal", this.token)
        .then((result) => {
          if (result.data.turntotal) {
            this.withdraw_fix = result.data.payload;
          } else {
            this.withdraw_fix = result.data.payload;
          }
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
      this.$axios
        .get("/history/withdraw", this.token)
        .then((result) => {
          result.data.payload.forEach((element) => {
            if (element.status == 3) this.withdraw_table.push(element);
          });
          $(".showloader").hide();
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
    } else {
      this.$router.push("/logout");
    }
  },
};
</script>

<style></style>
