<template>
  <div class="name container py-5" v-if="isLogin">
    <div class="row">Logout...</div>
  </div>
</template>

<script>
import $ from "jquery";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "logout",
  data() {
    return {};
  },
  created() {
    $(".showloader").show();
    setTimeout(() => {
      $(".showloader").hide();
    }, 4000);
  },
  computed: {
    ...mapGetters({
      isLogin: "isLogin",
      user: "user",
    }),
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
    }),
  },
  mounted() {
    $(".showloader").hide();
    this.$session.destroy();
    this.storeLogout();
    this.$router.push("/Login");
    // $(".showloader").hide();
  },
};
</script>

