<template>
  <div v-if="data != null">
    <div style="display: flex">
      <router-link
        to="/"
        class="pl-4"
        style="font-size: 45px; color: white; z-index: 99999999999"
      >
        <button class="btn btn-sm btn-info">ย้อนกลับ</button>
      </router-link>
      <iframe :src="data.url" frameborder="0" class="spinspinspin"></iframe>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      data: null,
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  methods: {
    getgamewheel() {
      this.$axios
        .get("/playgame_spin", this.token)
        .then((result) => {
          if (result.data.code == "SUCCESS") {
            this.data = result.data.payload;
          } else {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: result.data.msg,
              showConfirmButton: true,
              icon: "error",
            }).then((result) => {
              if (result) {
                this.$router.push("/");
              }
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getgamewheel();
  },
};
</script>

<style>
.spinspinspin {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0px;
}
</style>