<template>
  <div class="Redirect container">
    <div class="row justifty-content-center">
      <div class="col-12 col-md-8 col-lg-9"></div>
    </div>
  </div>
</template>

<script>
// import $ from "jquery";
import { mapGetters, mapActions } from "vuex";
export default {
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  mounted() {
    this.$axios
      .get("/is_login", this.token)
      .then((result) => {
        if (result.data.msg === "ERROR_NOT_FOUND_TOKEN") {
          this.$swal({
            title: "เกิดข้อผิดพลาด",
            text: "มีการเข้าสู่ระบบจากที่อื่น",
            icon: "error",
            timer: 3000,
            showConfirmButton: true,
          });
          this.$router.push("/logout");
        } else if (result.data.msg === "ERROR") {
          this.$swal({
            title: "เกิดข้อผิดพลาด",
            text: "มีการเข้าสู่ระบบจากที่อื่น",
            icon: "error",
            timer: 3000,
            showConfirmButton: true,
          });
          this.$router.push("/logout");
        } else if (result.data.msg === "USER_NOT_FOUND") {
          this.$swal({
            title: "เกิดข้อผิดพลาด",
            text: "มีการเข้าสู่ระบบจากที่อื่น",
            icon: "error",
            timer: 3000,
            showConfirmButton: true,
          });
          this.$router.push("/logout");
        } else {
          if (result.data.msg != "LOGOUT") {
            this.$session.set("isLogin", true);
            this.$session.set("token", result.data);
            this.storeLogin(result.data);
            if (this.user.active === 0) {
              this.$router.push("/Otp");
            } else if (this.$route.params.path === "Main") {
              this.$axios.get("/affiliateUpdate", this.token).then((result) => {
                return result;
              });
              this.$router.push("/");
            } else {
              this.$router.push("/" + this.$route.params.path);
            }
          } else {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: "มีการเข้าสู่ระบบจากที่อื่น",
              icon: "error",
              timer: 3000,
              showConfirmButton: true,
            });
            this.$router.push("/logout");
          }
        }
      })
      .catch((err) => {
        console.log(err);
        this.$swal({
          title: "เกิดข้อผิดพลาด",
          text: "เกิดข้อผิดพลาดของระบบ",
          icon: "error",
          timer: 3000,
          showConfirmButton: true,
        });
        this.$router.push("/logout");
      });
  },
};
</script>

<style></style>
