<template>
  <div class="History container" style="margin-top:80px;">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-8">
        <div class="row justify-content-center">
          <a class="col-4 px-2" @click="statusSelect = 'DEPOSIT'">
            <div :class="statusSelect === 'DEPOSIT' ? 'btn-history' : ''">
              <div class="color-main p-2 font-weight-bold">รายการฝาก</div>
            </div>
          </a>
          <a class="col-4 px-2" @click="statusSelect = 'WITHDRAW'">
            <div :class="statusSelect === 'WITHDRAW' ? 'btn-history' : ''">
              <div class="color-main p-2 font-weight-bold">รายการถอน</div>
            </div>
          </a>
          <a class="col-4 px-2" @click="statusSelect = 'BONUS'">
            <div :class="statusSelect === 'BONUS' ? 'btn-history' : ''">
              <div class="color-main p-2 font-weight-bold">โบนัสฟรี</div>
            </div>
          </a>
        </div>
        <mdb-card class="my-2 card-deposit" v-if="statusSelect === 'DEPOSIT'">
          <div class="p-2">
            <mdb-scrollbar class="Maxheighthistory">
              <mdb-tbl borderless class="mt-2">
                <mdb-tbl-head color="light">
                  <tr>
                    <td scope>
                      <span class="color-main">
                        <small>ID</small>
                      </span>
                    </td>
                    <td scope>
                      <span class="color-main">
                        <small>วันที่</small>
                      </span>
                    </td>
                    <td scope>
                      <span class="color-main">
                        <small>จำนวนเงินฝาก</small>
                      </span>
                    </td>
                    <td scope>
                      <span class="color-main">
                        <small>สถานะ</small>
                      </span>
                    </td>
                  </tr>
                </mdb-tbl-head>

                <mdb-tbl-body v-if="deposit">
                  <tr v-for="deposits in deposit" :key="deposits.id">
                    <td class="font-weith-bold">{{ deposits.id }}</td>
                    <td class="font-weith-bold">
                      {{ deposits.date | moment("YYYY-MM-DD") }}
                      {{ deposits.time.substr(0, 5) }}
                    </td>
                    <td class="font-weith-bold text-success">
                      {{ "+" + currencyFormat(deposits.amount) }}
                    </td>
                    <td class="font-weith-bold">
                      <span
                        class="badge badge-success"
                        v-if="deposits.status === 1"
                        >สำเร็จ</span
                      >
                      <span class="badge badge-warning" v-else
                        >กำลังดำเนินการ</span
                      >
                    </td>
                  </tr>
                </mdb-tbl-body>
              </mdb-tbl>
            </mdb-scrollbar>
          </div>
        </mdb-card>
        <mdb-card class="my-2 card-deposit" v-if="statusSelect === 'WITHDRAW'">
          <div class="p-2">
            <mdb-scrollbar class="Maxheighthistory">
              <mdb-tbl borderless>
                <mdb-tbl-head color="light">
                  <tr>
                    <th scope="col">
                      <span class="color-main">
                        <small>ID</small>
                      </span>
                    </th>
                    <th scope="col">
                      <span class="color-main">
                        <small>วันที่</small>
                      </span>
                    </th>
                    <th scope="col">
                      <span class="color-main">
                        <small>จำนวนเงินถอน</small>
                      </span>
                    </th>
                    <th scope="col">
                      <span class="color-main">
                        <small>สถานะ</small>
                      </span>
                    </th>
                  </tr>
                </mdb-tbl-head>

                <mdb-tbl-body v-if="withdraw">
                  <tr v-for="withdraws in withdraw" :key="withdraws.id">
                    <th scope="row">{{ withdraws.id }}</th>
                    <td>
                      {{ withdraws.date | moment("YYYY-MM-DD") }}
                      {{ withdraws.time.substr(0, 5) }}
                    </td>
                    <td class="text-danger">
                      {{ "-" + currencyFormat(withdraws.amount) }}
                    </td>
                    <td>
                      <span
                        class="badge badge-info"
                        v-if="withdraws.status === 2"
                        >รอโอนเงิน</span
                      >
                      <span
                        class="badge badge-warning"
                        v-else-if="withdraws.status === 3"
                        >รอตรวจสอบ</span
                      >
                      <span
                        class="badge badge-danger"
                        v-else-if="withdraws.status === 4"
                        >ยกเลิก</span
                      >
                      <span class="badge badge-success" v-else>สำเร็จ</span>
                      <span v-if="withdraws.status === 4" class="text-warning">
                        <br />
                        {{ withdraws.comment }}
                      </span>
                    </td>
                  </tr>
                </mdb-tbl-body>
              </mdb-tbl>
            </mdb-scrollbar>
          </div>
        </mdb-card>
        <mdb-card class="my-2 card-deposit" v-if="statusSelect === 'BONUS'">
          <div class="p-2">
            <mdb-scrollbar class="Maxheighthistory">
              <mdb-tbl borderless>
                <mdb-tbl-head color="light">
                  <tr>
                    <th scope="col">
                      <span class="color-main">
                        <small>ID</small>
                      </span>
                    </th>
                    <th scope="col">
                      <span class="color-main">
                        <small>วันที่</small>
                      </span>
                    </th>

                    <th scope="col">
                      <span class="color-main">
                        <small>โบนัส</small>
                      </span>
                    </th>
                    <th scope="col">
                      <span class="color-main">
                        <small>จำนวน</small>
                      </span>
                    </th>
                  </tr>
                </mdb-tbl-head>

                <mdb-tbl-body v-if="bonus">
                  <tr v-for="bonusx in bonus" :key="bonusx.id">
                    <td>{{ bonusx.id }}</td>
                    <td>
                      {{ bonusx.date | moment("YYYY-MM-DD") }}
                      {{ bonusx.time.substr(0, 5) }}
                    </td>
                    <td>{{ bonusx.title }}</td>
                    <td>{{ bonusx.bonus }}</td>
                  </tr>
                </mdb-tbl-body>
              </mdb-tbl>
            </mdb-scrollbar>
          </div>
        </mdb-card>
        <div class="text-left">
          <router-link to="/" class="btn btn-sm btn-main">ย้อนกลับ</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      statusSelect: "DEPOSIT",
      deposit: null,
      withdraw: null,
      bonus: null,
    };
  },
  computed: {
    ...mapGetters({
      isLogin: "isLogin",
      token: "token",
    }),
  },
  methods: {
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    bankNumberFormat(str) {
      return str.replace(/[0-9]/g, (x, index) => {
        if (index == 2 || index == 5) return x + "-";
        return x;
      });
    },
  },
  mounted() {
    if (this.isLogin) {
      $(".showloader").show();
      this.$axios
        .get("/history/deposit", this.token)
        .then((result) => {
          // $(".showloader").hide();
          this.deposit = result.data.payload;
        })
        .catch((error) => console.log(error));
      this.$axios
        .get("/history/withdraw", this.token)
        .then((result) => {
          this.withdraw = result.data.payload;
        })
        .catch((error) => console.log(error));
      this.$axios
        .get("/history/bonus", this.token)
        .then((result) => {
          $(".showloader").hide();
          this.bonus = result.data.payload;
        })
        .catch((error) => console.log(error));
    } else {
      // $(".showloader").hide();
      this.$router.push("/logout");
    }
  },
};
</script>

<style>
.Maxheighthistory {
  max-height: 62vh;
}
</style>
