<template>
  <div class="Main container py-2" style="margin-top: 120px">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-8 text-center">
        <mdb-carousel
          :items="items"
          :interval="8000"
          controlls
          indicators
        ></mdb-carousel>
        <div class="row">
          <router-link
            to="/Redirect/deposit"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/deposit.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">ฝากเงิน</small>
            </div>
          </router-link>
          <router-link
            to="/Redirect/withdraw"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/withdraw.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">ถอนเงิน</small>
            </div>
          </router-link>
          <router-link
            to="/Redirect/bonus"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div
                style="margin-bottom: -20px"
                class="text-right"
                v-if="banuslength > 0"
              >
                <badge :text="banuslength" imageClass="css-class-for-image" />
              </div>
              <div class="p-2">
                <img src="/images/menu/gift.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">โบนัสฟรี</small>
            </div>
          </router-link>
          <router-link
            to="/Redirect/affiliate"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/aff.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">แนะนำเพื่อน</small>
            </div>
          </router-link>
          <router-link
            to="/Redirect/cashback"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/cashback.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">CASHBACK</small>
            </div>
          </router-link>
          <router-link
            to="/Redirect/gamewheel"
            class="col-3 col-md-2 mt-1 text-center"
            v-if="checktime"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/wheel.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">กงล้อให้โชค</small>
            </div>
          </router-link>
          <router-link
            to="/Redirect/history"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/history.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">ประวัติ</small>
            </div>
          </router-link>
          <!-- <a href="javascript:start_wheel();" class="col-3 mt-1 text-center">
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/wheel.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">กงล้อให้โชค</small>
            </div>
          </a>-->
          <!-- <a href="javascript:start_wheel();">start_wheel();</a> -->
          <a @click="modal = true" class="col-3 col-md-2 mt-1 text-center">
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/settingbonus.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">ตั้งค่าโบนัส</small>
            </div>
          </a>
          <div>
            <mdb-modal :show="modal" @close="modal = false" centered>
              <mdb-modal-header class="color-main">
                <mdb-modal-title>ตั้งค่าโบนัส</mdb-modal-title>
              </mdb-modal-header>
              <mdb-modal-body>
                <div class="row justify-content-center">
                  <div class="col-4 text-center align-self-center mt-3">
                    <mdb-switch
                      v-model="switchValue"
                      offLabel
                      onLabel
                      classes="success pt-2"
                      @getValue="setBonus()"
                    />
                  </div>
                  <div
                    class="col-8 text-left align-self-center font-weight-bold mt-3"
                    v-if="bonus_config"
                    :class="switchValue === true ? 'color-main' : 'text-dark'"
                  >
                    เลือกไม่รับโบนัส / เลือกรับโบนัส
                    <br />
                    <span class="text-dark">
                      *เลือกเปิด เพื่อรับโบนัส
                      ทางระบบจะทำการเลือกโบนัสที่ดีที่สุดให้
                    </span>
                  </div>
                </div>
              </mdb-modal-body>
              <mdb-modal-footer>
                <mdb-btn color class="btn-main" @click.native="modal = false"
                  >ย้อนกลับ</mdb-btn
                >
              </mdb-modal-footer>
            </mdb-modal>
          </div>
          <router-link
            to="/Redirect/Dowload"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/download.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">ดาวน์โหลด</small>
            </div>
          </router-link>
          <a
            target="_bank"
            :href="$link_fbcontact"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/facebook.png?_=1" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">FACEBOOK</small>
            </div>
          </a>
          <a
            target="_bank"
            :href="$link_linecontact"
            class="col-3 col-md-2 mt-1 text-center"
          >
            <mdb-card class="my-2">
              <div class="p-2">
                <img src="/images/menu/line.png" width="100%" alt />
              </div>
            </mdb-card>
            <div class="text-dark">
              <small class="font-weight-bold">LINE@</small>
            </div>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
let getEvent = "https://allbet.asia/wheel/";
const jwt = require("jsonwebtoken");
import { mapGetters, mapActions } from "vuex";
import { VueImageBadge } from "vue-image-badge";
import firebase from "firebase";
var bannerRef = firebase.database().ref("banner");
export default {
  data() {
    return {
      items: [],
      banuslength: 0,
      modal: false,
      switchValue: true,
      bonus_config: null,
      checktime: null,
    };
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
    setBonus: function () {
      let payload = {
        is_bonus: this.switchValue === false ? 1 : 0,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      // $(".showloader").show();
      $(".showloader").show();
      this.$axios
        .post("/is_bonus", { token: token }, this.token)
        .then((response) => {
          if (response.data.code === "SUCCESS") {
            $(".showloader").hide();
            this.$axios
              .get("/is_login", this.token)
              .then((response) => {
                if (response.data.msg != "LOGOUT") {
                  this.$session.set("isLogin", true);
                  this.$session.set("token", response.data);
                  this.storeLogin(response.data);
                } else {
                  this.$swal({
                    title: "เกิดข้อผิดพลาด",
                    text: "มีการเข้าสู่ระบบจากที่อื่น",
                    type: "error",
                    timer: 3000,
                    showConfirmButton: false,
                  });
                  this.$router.push("/logout");
                }
              })
              .catch(function (error) {
                console.log(error);
              });
          } else {
            $(".showloader").hide();
            console.log("false");
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  components: {
    badge: VueImageBadge,
  },
  mounted() {
    this.checktime =
      this.$moment().format("YYYY-DD-MM") >=
      this.$moment().format("2020-11-20");
    console.log(this.checktime);
    if (this.$session.get("isLogin")) {
      if (this.token) {
        this.switchValue = this.user.is_bonus === 1 ? true : false;
        $(".showloader").show();

        $.getScript(
          getEvent + "?username=" + this.user.username + "&service=JOKER24H"
        );
        this.$axios
          .get("/free_bonuslist", this.token)
          .then((result) => {
            if (result.data.code !== "ERROR") {
              this.banuslength = result.data.payload.length;
            }
          })
          .catch((err) => {
            $(".showloader").hide();
            console.log(err);
          });
        this.$axios
          .get("/bonus", this.token)
          .then((result) => {
            this.bonus_config = result.data.payload;
            $(".showloader").hide();
          })
          .catch(function (error) {
            $(".showloader").hide();
            console.log(error);
          });
        bannerRef.orderByKey().on("value", (snap) => {
          this.bannerPic = snap.val();
          for (var p = 0; p < this.bannerPic.length; p++) {
            this.items.push({
              img: true,
              src: this.bannerPic[p],
            });
          }
        });
      } else {
        this.$router.push("/login");
        console.log("No token");
      }
    } else {
      this.$router.push("/login");
    }
  },
};
</script>

<style></style>
