
import '@fortawesome/fontawesome-free/css/all.min.css'
import 'bootstrap-css-only/css/bootstrap.min.css'
import 'mdbvue/lib/css/mdb.min.css'

import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueSession from "vue-session";
import $ from "jquery";
import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)
Vue.prototype.$ = $
Vue.config.productionTip = false
Vue.use(VueSession);
import Notify from 'mdbvue/lib/components/Notify'
Vue.use(Notify)
import * as mdbvue from 'mdbvue'
import axios from 'axios';
const WEBSERVICE = 'MABUUSLOTXO'
const CONFIG = require('./config.json').service[WEBSERVICE]
axios.defaults.baseURL = CONFIG.API;
Vue.prototype.$axios = axios
Vue.prototype.$urlapi = CONFIG.API
const keypayload = "ABAsecretKey*";
Vue.prototype.$keypayload = keypayload;
for (const component in mdbvue) {
  Vue.component(component, mdbvue[component])
}
import { VueImageBadge } from 'vue-image-badge'

Vue.use(VueImageBadge);
window.$ = require("jquery");
import VueSweetalert2 from 'vue-sweetalert2';
// If you don't need the styles, do not connect
import 'sweetalert2/dist/sweetalert2.min.css';
Vue.use(VueSweetalert2);
import moment from "vue-moment";
Vue.use(moment);

import momentjs from "moment";
const momentsjs = momentjs
const jwt = require("jsonwebtoken");

Vue.prototype.$jwt = jwt
Vue.prototype.$momentjs = momentsjs
Vue.prototype.$linkaff = CONFIG.linkaff
Vue.prototype.$dowloadlink = CONFIG.downloadgame
Vue.prototype.$link_linealert = CONFIG.line_alert
Vue.prototype.$link_firebase = CONFIG.firebaseurl
Vue.prototype.$link_linecontact = CONFIG.contactline
Vue.prototype.$link_fbcontact = CONFIG.contactfb
Vue.prototype.$qrcode_linealert = CONFIG.link_QRcodelinealert
var vueAwesomeCountdown = require('vue-awesome-countdown').default;

Vue.use(vueAwesomeCountdown);
const VueInputMask = require("vue-inputmask").default;
Vue.use(VueInputMask);
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
