<template>
  <div class="Profile container" style="margin-top:80px;">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-8">
        <span class="font-weight-bold text-dark h5">จำนวนเงินคงเหลือ</span>
        <br />
        <span class="font-weight-bold color-main h3">{{
          currencyFormat(amount)
        }}</span>
        <div class="row justify-content-center mt-3">
          <div class="col-6">
            <span class="font-weight-bold text-dark h5">แต้มสะสม</span>
            <br />
            <span class="font-weight-bold color-main h4">{{ user.point }}</span>
          </div>
          <div class="col-6">
            <span class="font-weight-bold text-dark h5">เพชรสะสม</span>
            <br />
            <span class="font-weight-bold color-main h4">{{
              user.diamond
            }}</span>
          </div>
        </div>
        <mdb-card class="mt-3 card-deposit">
          <div class="p-2">
            <span class="h5 font-weight-bold color-toppic">บัญชีของคุณ</span>
            <div class="row justify-content-center">
              <div class="col-12 col-md-8 col-lg-8 mt-3">
                <div class="row justify-content-center container">
                  <div class="col-4 col-lg-5 align-self-center">
                    <img
                      :src="'/images/bank/' + user.bank_code + '.png'"
                      width="80%"
                      alt
                    />
                  </div>
                  <div
                    class="col-8 col-lg-7 text-dark text-left font-weight-bold align-self-center"
                  >
                    {{ user.bank_type }}
                    <br />
                    {{ bankNumberFormat(user.bank_number) }}
                    <a
                      v-clipboard:copy="user.bank_number"
                      v-clipboard:success="onCopy"
                    >
                      <mdb-badge color="warning">
                        <small>คัดลอก</small>
                      </mdb-badge>
                    </a>
                    <div v-if="user.bank_code === 'SCB'">
                      {{ user.fullname_bank }}
                      <a @click="Editbank()">
                        <mdb-badge color="warning">
                          <small>แก้ไข</small>
                        </mdb-badge>
                      </a>
                    </div>
                    <div v-else>
                      {{ user.full_name }}
                    </div>
                  </div>
                  <div class="col-12 mt-3">
                    <label class="h5 color-toppic font-weight-bold"
                      >จำนวนเงินคงเหลือ</label
                    >

                    <label class="h3 color-main font-weight-bold ml-2"
                      >{{ currencyFormat(amount) }} บาท</label
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </mdb-card>
        <mdb-card class="mt-3 card-deposit">
          <div class="p-3 text-left">
            <label class="h5 color-main font-weight-bold mt-2"
              >ยูสเซอร์เนม : {{ user.username }}</label
            >
            <br />

            <label class="h5 color-main font-weight-bold mt-2"
              >รหัสผ่าน : {{ user.password }}</label
            >
          </div>
        </mdb-card>
        <div class="text-left my-3">
          <router-link to="/" class="btn btn-sm btn-main">ย้อนกลับ</router-link>
        </div>
        <div class="row">
          <mdb-modal :show="subs" @close="subs = false" centered>
            <mdb-modal-header class="text-center">
              <mdb-modal-title>
                <mdb-icon far icon="edit" class="color-main" />
                <span class="color-main">แก้ไขชื่อ</span>
              </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
              <div v-if="stepNamebank === 1">
                <div class="text-left color-main">
                  <div class>ชื่อจริง</div>
                </div>
                <div class="col-12 text-left px-0">
                  <div>
                    <small class="text-primary"
                      >กรุณากรอกเป็นภาษาอังกฤษ ตัวพิมพ์ใหญ่ A-Z</small
                    >
                  </div>
                  <input
                    type="text"
                    id="example1"
                    class="form-control border-black"
                    v-model="FistnameSCB"
                    onpaste="return false"
                    oninput="this.value = this.value.toUpperCase()"
                    onkeypress="return event.charCode >= 65 && event.charCode <= 90 || event.charCode >= 97 && event.charCode <= 122"
                  />
                </div>

                <div class="text-left color-main">
                  <div class>นามสกุล</div>
                </div>

                <div class="text-left">
                  <small class="text-primary"
                    >กรุณากรอกเป็นภาษาอังกฤษ ตัวพิมพ์ใหญ่ A-Z</small
                  >
                </div>
                <input
                  type="text"
                  id="example1"
                  class="form-control border-black"
                  onpaste="return false"
                  oninput="this.value = this.value.toUpperCase()"
                  onkeypress="return event.charCode >= 65 && event.charCode <= 90 || event.charCode >= 97 && event.charCode <= 122"
                  v-model="LastnameSCB"
                />
              </div>
              <div v-if="stepNamebank === 2">
                <div class="h3 text-center mt-3 color-main">
                  {{ FistnameSCB.toUpperCase() }}
                  {{ LastnameSCB.toUpperCase() }}
                </div>
              </div>
            </mdb-modal-body>
            <mdb-modal-footer>
              <div class="col-12">
                <div class="row">
                  <div class="col-6">
                    <a class @click="subs = false">
                      <div class="btn-block button-border-css text-center">
                        <span
                          class="p-1 button-border-line-white-css color-main"
                          >ย้อนกลับ</span
                        >
                      </div>
                    </a>
                  </div>
                  <div
                    class="col-6 align-self-center"
                    v-if="stepNamebank === 1"
                  >
                    <button
                      class="btn btn-block btn-main p-2 text-white"
                      @click="checknameStep()"
                    >
                      <span>ตกลง</span>
                    </button>
                  </div>
                  <div
                    class="col-6 align-self-center"
                    v-if="stepNamebank === 2"
                  >
                    <button
                      class="btn btn-block btn-main p-2 text-white"
                      @click="Submit()"
                    >
                      <span>ยืนยันการกรอกข้อมูล</span>
                    </button>
                  </div>
                </div>
              </div>
            </mdb-modal-footer>
          </mdb-modal>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const jwt = require("jsonwebtoken");
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      stepNamebank: 1,
      subs: false,
      FistnameSCB: null,
      LastnameSCB: null,
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  mounted() {
    let str = this.user.fullname_bank;
    let first_name_log = str.split(" ")[0];
    let last_name_log = str.split(" ")[1];
    this.FistnameSCB = first_name_log;
    this.LastnameSCB = last_name_log;
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
      checkVersion: "checkVersion",
    }),
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    bankNumberFormat(str) {
      return str.replace(/[0-9]/g, (x, index) => {
        if (index == 2 || index == 5) return x + "-";
        return x;
      });
    },
    onCopy: function(e) {
      this.$swal({
        title: "คัดลอกเลขบัญชี",
        text: "คัดลอกเลขบัญชี : " + e.text,
        icon: "success",
        allowEscapeKey: false,
        allowOutsideClick: false,
        showConfirmButton: true,
      });
    },
    Editbank() {
      if (this.user.fullname_bank != null) {
        this.subs = true;
      } else {
        console.log("Null");
      }
    },
    checknameStep() {
      if (this.FistnameSCB != null && this.LastnameSCB != null) {
        this.stepNamebank = 2;
      } else {
        this.$swal({
          title: "เกิดข้อผิดพลาด",
          text: "กรุณากรอกข้อมูลให้ครบถ้วน",
          icon: "warning",
          allowEscapeKey: false,
          allowOutsideClick: false,
          showConfirmButton: true,
        });
      }
    },
    Submit() {
      // $(".preloader").show();
      let payload = {
        mr: this.fistN,
        first_name: this.FistnameSCB.toUpperCase(),
        last_name: this.LastnameSCB.toUpperCase(),
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });
      this.$axios
        .post("/updateBankname", { token: token }, this.token)
        .then((response) => {
          // $(".preloader").hide();
          if (response.data.code === "SUCCESS") {
            this.$swal({
              title: "สำเร็จ",
              html: response.data.msg.toUpperCase(),
              icon: "success",
              timer: false,
              showConfirmButton: true,
              allowEscapeKey: false,
              allowOutsideClick: false,
            }).then((result) => {
              if (result.value) {
                this.$axios
                  .get("/is_login", this.token)
                  .then((response) => {
                    if (response.data.msg != "LOGOUT") {
                      this.$session.set("isLogin", true);
                      this.$session.set("token", response.data);
                      this.storeLogin(response.data);
                      this.subs = false;
                      this.stepNamebank = 1;
                    } else {
                      this.$swal({
                        title: "เกิดข้อผิดพลาด",
                        text: "มีการเข้าสู่ระบบจากที่อื่น",
                        type: "error",
                        timer: 3000,
                        showConfirmButton: false,
                      });
                      this.$router.push("/logout");
                    }
                  })
                  .catch(function(error) {
                    console.log(error);
                  });
              }
            });
          } else {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              text: response.data.msg,
              type: "error",
              timer: false,
              showConfirmButton: false,
            });
          }
        });
    },
  },
};
</script>

<style></style>
