import Vue from 'vue'
import Vuex from 'vuex'
import VueSession from 'vue-session'
import axios from 'axios'
import jwt from 'jsonwebtoken'
const WEBSERVICE = 'MABUUSLOTXO'
const CONFIG = require('./config.json').service[WEBSERVICE]
Vue.use(Vuex)
Vue.use(VueSession)

const apiservice = CONFIG.API

const state = {
  isLogin: false,
  user: null,
  amount: 0,
  token: null,
  service: WEBSERVICE,
  version: '1.0/9/6/2563',
  rank: 1,
  rankcurrent: 9999
}

const getters = {
  isLogin: state => {
    return state.isLogin
  },
  user: state => {
    return state.user
  },
  amount: state => {
    return state.amount
  },
  token: state => {
    return state.token
  },
  server: state => {
    return state.server
  },
  version: state => {
    return state.version
  }
}

const mutations = {
  isLogin: (state, payload) => {
    state.isLogin = payload
  },
  user: (state, payload) => {
    state.user = payload
  },
  amount: (state, payload) => {
    state.amount = payload
  },
  token: (state, payload) => {
    state.token = payload
  }
}
const actions = {
  updateAmount: ({ commit }) => {
    axios.get(apiservice + '/amount', state.token).then(result => {
      commit('amount', result.data)
    })
  },
  login: ({ commit }, payload) => {
    commit('isLogin', true)
    let auth = {
      headers: {
        Authorization: 'Bearer ' + payload
      }
    }
    commit('token', auth)
    let user = jwt.decode(payload)
    if (user) {

      commit('user', user.results[0])
      axios.get(apiservice + '/amount',
        state.token).then(result => {
          commit('amount', result.data)
        })
    }
  },
  logout: ({ commit }) => {
    commit('isLogin', false)
    commit('user', null)
    commit('token', null)
  }

}
export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})
