<template>
  <div class="Header pt-5" v-if="$route.name != 'login'">
    <div class="row justify-content-center fixed-top">
      <div class="col-12">
        <mdb-navbar class="navbar" style="z-index: 2">
          <mdb-navbar-brand class="py-2"></mdb-navbar-brand>
        </mdb-navbar>
      </div>
      <div class="col-12"></div>
      <div class="row justify-content-center container">
        <div
          class="col-11 col-md-8 col-lg-8 px-0"
          style="margin-top: -45px; margin-left: -15px; z-index: 4"
          v-if="$route.name != 'register'"
        >
          <div class="text-right">
            <a @click="show = !show">
              <mdb-icon icon="ellipsis-v fa-2x" />
            </a>
          </div>
        </div>
        <div
          class="col-8 col-md-6 col-lg-7 text-center"
          style="margin-top: -60px; z-index: 5"
        >
          <router-link to="/Redirect/Main" href>
            <img src="/logoslotxo.png" width="80px" alt />
          </router-link>
        </div>
      </div>
      <div class="Profile row justify-content-center container" v-if="isLogin">
        <div
          class="col-12 col-md-8 col-lg-8 px-0"
          style="margin-top: -38px; z-index: 0"
        >
          <div
            class="header-profile pt-4 py-3 container"
            v-if="$route.name === 'main'"
          >
            <div class="text-left mt-2" v-if="user">
              ยูสเซอร์เนม :
              <span class="color-secondary">{{ user.username }}</span>
              <a
                v-clipboard:copy="user.username"
                v-clipboard:success="onCopy"
                class="ml-1"
              >
                <mdb-badge color="info">
                  <small>คัดลอก</small>
                </mdb-badge>
              </a>
              <br />รหัสผ่าน :
              <span class="color-secondary">{{ user.password }}</span>
              <br />ยอดเงินคงเหลือ :
              <span class="color-secondary">{{ currencyFormat(amount) }}</span>
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'deposit'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              ฝากเงิน
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'withdraw'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              <span>ถอนเงิน</span>
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'bonus'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              โบนัสฟรี
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'affiliate'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              แนะนำเพื่อน
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'history'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              ประวัติการใช้งาน
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'profile'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              บัญชีข้อมูลผู้ใช้
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'dowload'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              ดาวน์โหลด
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'cashback'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              CASHBACK
            </div>
          </div>
          <div
            class="header-tap pt-4 py-2 container"
            v-if="$route.name === 'Depositdesimal'"
          >
            <div class="text-left h4 pt-2 font-weight-bold" v-if="user">
              ฝากเงินทศนิยม
            </div>
          </div>
        </div>
      </div>

      <!--/.Navbar-->
      <mdb-side-nav-2 v-model="show" over right v-if="isLogin">
        <div slot="header" class="row justify-content-center side-navber">
          <div class="col-12 text-right">
            <a @click="show = !show" class="text-white">
              <mdb-icon icon="ellipsis-v fa-2x" />
            </a>
          </div>
          <div class="col-12">
            <div class="row justify-content-center text-left">
              <div class="col-12 text-center">
                <div class="text-center">
                  <img src="/logoslotxo.png" width="40%" alt />
                </div>
              </div>
              <div class="col-7 text-white">
                <span>ยอดเงินคงเหลือ</span>
              </div>
              <div class="col-5 color-secondary">
                <span>{{ currencyFormat(amount) }}</span>
              </div>
              <div class="col-7 text-white">
                <span>แต้มสะสม</span>
              </div>
              <div class="col-5 color-secondary">
                <span>{{ user.point }}</span>
              </div>
              <div class="col-7 text-white">
                <span>เพชรสะสม</span>
              </div>
              <div class="col-5 color-secondary">
                <span>{{ user.diamond }}</span>
              </div>
            </div>
          </div>
          <div class="col-12">
            <div class="row justify-content-center text-left text-white">
              <div class="col-12">
                <div class="border-top my-3"></div>
                <a @click="Redirectpage('Main')" class="text-white">
                  <div class="mt-2">หน้าหลัก</div>
                </a>
                <a @click="Redirectpage('profile')" class="mt-4 text-white">
                  <div class="mt-2">ดูข้อมูลผู้ใช้</div>
                </a>
                <a @click="Redirectpage('dowload')" class="mt-4 text-white">
                  <div class="mt-2">ดาวน์โหลด</div>
                </a>

                <a
                  target="game"
                  :href="$link_linecontact"
                  class="mt-2 text-white"
                >
                  <div class="mt-2 text-success">LINE</div>
                </a>

                <a
                  target="_target"
                  :href="$link_fbcontact"
                  class="mt-2 text-white"
                >
                  <div class="mt-2 text-primary">FACEBOOK</div>
                </a>

                <a @click="Redirectpage('logout')" class="mt-2">
                  <div class="mt-2 color-secondary">ออกจากระบบ</div>
                </a>
              </div>
            </div>
          </div>
        </div>
      </mdb-side-nav-2>
    </div>
  </div>
</template>

<script>
import jwt from "jsonwebtoken";
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      show: false,
    };
  },
  methods: {
    ...mapActions({
      storeLogin: "login",
      storeLogout: "logout",
    }),
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    onCopy: function (e) {
      this.$swal({
        title: "คัดลอกยูสเซอร์เนม",
        text: "คัดลอกยูสเซอร์เนม : " + e.text,
        icon: "success",
        allowEscapeKey: false,
        allowOutsideClick: false,
        showConfirmButton: true,
      });
    },
    Redirectpage(p) {
      this.$router.push("/Redirect/" + p);
      this.show = false;
    },
  },
  computed: {
    ...mapGetters({
      isLogin: "isLogin",
      user: "user",
      amount: "amount",
      token: "token",
    }),
  },
  mounted() {
    this.$session.start();
    if (this.$session.get("isLogin")) {
      let tokenSession = this.$session.get("token");
      let user = jwt.decode(tokenSession);

      let payload = {
        phone_number: user.results[0].phone_number,
        password: user.results[0].password,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "300s",
      });

      this.$axios
        .post("/login", {
          token: token,
        })
        .then((result) => {
          if (result.data.msg != "USER_NOT_FOUND") {
            this.$session.set("version", this.version);
            this.$session.set("isLogin", true);
            this.$session.set("token", result.data);
            this.storeLogin(result.data);
            this.$router.push("/Redirect/Main");
          } else {
            console.log("User error");
          }
        })
        .catch((err) => {
          console.log(err);
          this.$swal({
            title: "เกิดข้อผิดพลาด",
            html: "ระบบเกิดข้อผิดพลาดกรุณาแจ้งพนักงาน",
            showConfirmButton: true,
            icon: "error",
          });
        });
    }
  },
};
</script>

<style></style>
