<template>
  <div class="Footer">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-4" style="margin-bottom:80px;">
        <div class="row justify-content-center color-main mt-5">
          <div
            class="col-6 col-md-4 col-lg-6 text-center"
            v-for="(games, index) in game"
            :key="index"
          >
            <a
              :href="games.link"
              target="_blank"
              class="text-dark font-weight-bold"
              rel="noreferrer"
            >
              <img
                :src="games.image"
                class="img-responsive"
                width="70%"
                alt="image"
              />
              <p style="font-size:12px!important">{{ games.title }}</p>
            </a>
          </div>
          <center class="text-dark mb-2">
            Copyright © MABUUXO.com All Right Reserved
          </center>
        </div>
      </div>
    </div>
    <div
      class="row justify-content-center fixed-bottom"
      v-if="$route.name !== 'login'"
    >
      <div class="row justify-content-center container">
        <div
          class="col-12 col-md-8 col-lg-8 px-0"
          style="margin-top:-38px;z-index:0;"
        >
          <div class="navbar-bottom mt-3">
            <div class="row py-2">
              <router-link
                to="/Redirect/Main"
                class="text-white col align-self-center"
              >
                <img src="/images/footer/home.png" width="30px" alt />
                <div for>
                  <small>หน้าหลัก</small>
                </div>
              </router-link>
              <router-link
                to="/Redirect/deposit"
                class="col align-self-center text-white"
              >
                <img src="/images/footer/bank.png" width="30px" alt />
                <div for>
                  <small>ฝากเงิน</small>
                </div>
              </router-link>
              <a @click="playgame()" class="col align-self-center text-white">
                <img src="/images/footer/game.png" width="50px" alt />
                <div for>
                  <small>เล่นเกม</small>
                </div>
              </a>
              <router-link to="/Redirect/profile" class="col align-self-user">
                <img src="/images/footer/user.png" width="30px" alt />
                <div for class="text-white">
                  <small>ข้อมูล</small>
                </div>
              </router-link>
              <router-link to="/Redirect/logout" class="col align-self-center">
                <!-- <img src="/images/footer/user.png" width="30px" alt /> -->
                <mdb-icon icon="power-off fa-2x" class="text-white" />
                <div for class="text-white">
                  <small>ล็อกเอ้าท์</small>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
var datafootergameRef = firebase.database().ref("game");
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      game: null,
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  methods: {
    playgame() {
      // var formPlaygame = document.createElement("form");
      // formPlaygame.setAttribute("method", "post");
      // formPlaygame.setAttribute("action", this.$linkgame);
      // formPlaygame.setAttribute("target", "_blank");
      // var i = document.createElement("input");
      // i.setAttribute("type", "hidden");
      // i.setAttribute("name", "username");
      // i.setAttribute("value", this.user.username);
      // formPlaygame.appendChild(i);
      // var p = document.createElement("input");
      // p.setAttribute("type", "hidden");
      // p.setAttribute("name", "password");
      // p.setAttribute("value", this.user.password);
      // formPlaygame.appendChild(p);
      // document.body.appendChild(formPlaygame);
      // formPlaygame.submit();
      var formPlaygame = document.createElement("form");
      formPlaygame.setAttribute("method", "post");
      formPlaygame.setAttribute("action", "https://www.slotxo.com/SignIn");
      formPlaygame.setAttribute("target", "_blank");
      var i = document.createElement("input");
      i.setAttribute("type", "hidden");
      i.setAttribute("name", "Username");
      i.setAttribute("value", this.user.username);
      formPlaygame.appendChild(i);
      var p = document.createElement("input");
      p.setAttribute("type", "hidden");
      p.setAttribute("name", "Password");
      p.setAttribute("value", this.user.password);
      formPlaygame.appendChild(p);
      var a = document.createElement("input");
      a.setAttribute("type", "hidden");
      a.setAttribute("name", "Action");
      a.setAttribute("value", "GameIndex");
      formPlaygame.appendChild(a);
      var gamecode = document.createElement("input");
      gamecode.setAttribute("type", "hidden");
      gamecode.setAttribute("name", "Action");
      formPlaygame.appendChild(gamecode);
      var submit = document.createElement("input");
      submit.setAttribute("type", "hidden");
      submit.setAttribute("name", "agree");
      formPlaygame.appendChild(submit);
      document.body.appendChild(formPlaygame);
      formPlaygame.submit();
    },
  },
  mounted() {
    datafootergameRef.on("value", (snap) => {
      this.game = snap.val();
    });
  },
};
</script>

<style></style>
