<template>
  <div class="Bonus container" style="margin-top:80px;">
    <div class="row justify-content-center">
      <div class="col-12 col-md-8 col-lg-8">
        <div class="text-dark" v-if="bonus === null">
          <mdb-card class="my-2 card-deposit my-4">
            <div class="p-2 h4">
              <p class="mt-3 font-weight-bold">คุณยังไม่มีโบนัสในขณะนี้</p>
            </div>
          </mdb-card>
        </div>
        <div v-if="bonus">
          <mdb-card
            class="my-2 card-deposit my-4"
            v-for="(credit, index) in bonus"
            :key="index"
          >
            <div class="p-2">
              <div class="row justify-content-center">
                <div class="col-3 text-right align-self-center">
                  <img src="/images/bonus/gift.png" width="100%" alt />
                </div>
                <div class="col-5 text-left align-self-center">
                  <strong class="color-main font-weight-bold">
                    เครดิตฟรี {{ currencyFormat(credit.bonus) }}
                    <br />
                    <small>
                      กดรับก่อน
                      {{ credit.end_time | moment("DD/MM/YYYY hh:mm") }}
                    </small>
                  </strong>
                </div>
                <div class="col-4 col-md-3 align-self-center">
                  <mdb-btn
                    tag="a"
                    class="btn-block btn btn-sm"
                    color="info"
                    @click="Bonus(credit.id, credit.trunover)"
                  >
                    <div class="my-2">
                      <span class="mt-3">รับโบนัส</span>
                    </div>
                  </mdb-btn>
                </div>
              </div>
            </div>
          </mdb-card>
        </div>
        <div class="text-left">
          <router-link to="/" class="btn btn-sm btn-main">ย้อนกลับ</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
const jwt = require("jsonwebtoken");
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      bonus: null,
    };
  },
  methods: {
    currencyFormat(n) {
      n = parseFloat(n);
      return n.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
    },
    Bonus(id, trunover) {
      let payload = {
        id: id,
      };
      let token = jwt.sign(payload, this.$keypayload, {
        expiresIn: "600s",
      });
      this.$axios
        .post("/get_freebonus", { token: token }, this.token)
        .then((result) => {
          console.log(result.data.code);
          if (result.data.code != "ERROR") {
            return (
              result,
              this.$swal({
                title: "รับโบนัสสำเร็จ",
                icon: "success",
                timer: 8000,
                html: "ต้องทำเทิร์น " + trunover,
                showConfirmButton: true,
                allowOutsideClick: false,
                allowEscapeKey: false,
              }).then((response) => {
                if (response) {
                  this.$router.push("/Redirect/Main");
                }
              })
            );
          } else {
            this.$swal({
              title: "เกิดข้อผิดพลาด",
              icon: "error",
              timer: 8000,
              html: result.data.msg,
              showConfirmButton: true,
              allowOutsideClick: false,
              allowEscapeKey: false,
            }).then((response) => {
              if (response) {
                this.$router.push("/Redirect/Main");
              }
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  computed: {
    ...mapGetters({
      user: "user",
      isLogin: "isLogin",
      amount: "amount",
      token: "token",
      version: "version",
    }),
  },
  mounted() {
    if (this.token) {
      $(".showloader").show();
      this.$axios
        .get("/free_bonuslist", this.token)
        .then((result) => {
          $(".showloader").hide();
          if (result.data.code !== "ERROR") {
            this.bonus = result.data.payload;
          } else {
            this.bonus = null;
          }
        })
        .catch((err) => {
          $(".showloader").hide();
          console.log(err);
        });
    }
  },
};
</script>

<style></style>
